import server from '../../../common/server';

//查询主页面
export function getDeptIncomeDate(keyword, startDate, endDate) {
  return server.get({
    url: 'platform/deptincomecontrastiveanalysis/getDeptIncomeContrastiveAnalysisControllerData',
    params: {
      startDate: startDate,
      endDate: endDate,
    },
  });
}
export function downloadTemp(type,start,end) {
  let url = '';
  if (type === '导出') {
    url = '/platform/deptincomecontrastiveanalysis/export';
  }/* else {
    url = '/platform/deptincomecontrastiveanalysis/exportDeptBzTemplate';
  }*/
  return server.downloadFile({
    url: url,
    params:{
      start:start,
      end:end,
    }
  });
}