import server from '@/common/server';

export function getListDictType(keyword, pageData) {
  return server.get({
    url: '/base/dicttype/listDictTypeVOsPage',
    params: {
      keyword: keyword,
      'pageData.limit': pageData.limit,
      'pageData.order': pageData.order,
      'pageData.orderField': pageData.orderField,
      'pageData.page': pageData.page,
    },
  });
}

export function updateDictType(dictTypeData) {
  return server.put({
    url: '/base/dicttype',
    params: {
      id: dictTypeData.id,
      name: dictTypeData.name,
      value: dictTypeData.value,
    },
  });
}

export function createDictType(dictTypeData) {
  return server.post({
    url: '/base/dicttype',
    params: {
      name: dictTypeData.name,
      value: dictTypeData.value,
    },
  });
}

export function deleteDictType(value) {
  return server.del({
    url: '/base/dicttype',
    params: [value],
  });
}
//以下接口可用 以上接口待完成
export function getListDict(value) {
  return server.get({
    url: '/base/dict/listDictVOs',
    params: {
      type: value,
    },
  });
}

export function updateDict(typeData) {
  return server.put({
    url: '/base/dict',
    params: {
      id: typeData.id,
      desc: typeData.desc,
      name: typeData.name,
      value: typeData.value,
    },
  });
}

export function createDict(typeData) {
  return server.post({
    url: '/base/dict',
    params: {
      desc: typeData.desc,
      name: typeData.name,
      value: typeData.value,
      type: typeData.type,
    },
  });
}

export function deleteDict(id) {
  return server.del({
    url: '/base/dict',
    params: [id],
  });
}
