<template>
  <div>
    <div>
      <Row>
        <Col span="12">
          <div style="display: flex; justify-content: space-between; width: 98%; margin-bottom: 10px">
            <div style="display: flex">
              <div>
                <Input
                  search
                  v-model.trim="keyword"
                  @on-search="queryList"
                  clearable
                  placeholder="按名称搜索"
                  style="width: 180px"
                ></Input>
              </div>
            </div>
            <div>
              <Button type="primary" @click="queryList" icon="md-search" style="margin-right: 5px">查询</Button>
              <Button type="primary" @click="showCreateDictTypeMod('create')" icon="md-add" style="margin-right: 5px"
                >新增</Button
              >
              <Button type="primary" @click="showUpdateDictTypeMod('update')" icon="md-create" style="margin-right: 5px"
                >修改</Button
              >
              <Button type="error" @click="showDel" icon="md-trash">删除</Button>
            </div>
          </div>
        </Col>
        <Col span="12">
          <div style="display: flex; justify-content: space-between; width: 100%; margin-bottom: 10px">
            <div style="display: flex"></div>
            <div>
              <Button type="primary" @click="showCreateDictMod('create2')" icon="md-add" style="margin-right: 5px"
                >新增</Button
              >
              <Button type="primary" @click="showUpdateDictMod('update2')" icon="md-create" style="margin-right: 5px"
                >修改</Button
              >
              <Button type="error" @click="showDel2" icon="md-trash">删除</Button>
            </div>
          </div>
        </Col>
      </Row>
      <Row>
        <Col span="12">
          <div style="width: 98%">
            <Table
              border
              ref="table"
              size="small"
              :height="tableHeight"
              :columns="dataMain.columns"
              highlight-row
              @on-current-change="handleRowChange"
              :data="dataMain.data"
              tooltip-theme="light"
              :loading="dictTypeTableLoading"
            >
            </Table>
            <Page
              ref="page"
              style="margin-top: 10px"
              align="right"
              :total="dataMain.total"
              :current="dataMain.pageNumber"
              :page-size="dataMain.pageSize"
              :page-size-opts="[10, 20, 50, 100]"
              show-elevator
              show-total
              show-sizer
              @on-change="pageChangeTable"
              @on-page-size-change="pageSizeChange"
            />
          </div>
        </Col>
        <Col span="12">
          <div style="">
            <Table
              border
              ref="table2"
              size="small"
              :height="tableHeight"
              :columns="dataMain.columns2"
              highlight-row
              @on-current-change="handleRowChange2"
              :data="dataMain.data2"
              tooltip-theme="light"
              :loading="dictTableLoading"
            >
            </Table>
          </div>
        </Col>
      </Row>

      <!--新建模态框-->
      <Modal
        width="500px"
        v-model="showDictTypeModal"
        :title="dictTypeModalTitle"
        :footerHide="true"
        :mask-closable="false"
        :closable="true"
        @on-visible-change="closeFromCorner"
      >
        <i-form class="modHeight" ref="create" :model="dataMain.currentRow" :rules="validation_data" :label-width="100">
          <FormItem label="编码：" prop="value">
            <Input v-model="dataMain.currentRow.value" maxlength="500" placeholder="请输入" />
          </FormItem>
          <FormItem label="名称：" prop="name">
            <Input v-model="dataMain.currentRow.name" maxlength="500" placeholder="请输入" />
          </FormItem>
        </i-form>

        <div class="ivu-modal-footer">
          <slot name="footer">
            <Button @click="closeMod">取 消</Button>
            <Button type="primary" @click="add_success()">确 定</Button>
          </slot>
        </div>
      </Modal>

      <Modal
        width="500px"
        v-model="showDictModal"
        :title="dictModalTitle"
        :footerHide="true"
        :mask-closable="false"
        :closable="true"
        @on-visible-change="closeFromCorner"
      >
        <i-form
          class="modHeight"
          ref="create2"
          :model="dataMain.currentRow2"
          :rules="validation_data2"
          :label-width="100"
        >
          <FormItem label="值：" prop="value">
            <Input v-model="dataMain.currentRow2.value" maxlength="500" placeholder="请输入" />
          </FormItem>
          <FormItem label="名称：" prop="name">
            <Input v-model="dataMain.currentRow2.name" maxlength="500" placeholder="请输入" />
          </FormItem>
          <FormItem label="描述：" prop="desc">
            <Input v-model="dataMain.currentRow2.desc" maxlength="500" placeholder="请输入" />
          </FormItem>
        </i-form>

        <div class="ivu-modal-footer">
          <slot name="footer">
            <Button @click="closeMod2">取 消</Button>
            <Button type="primary" @click="add_success2()">确 定</Button>
          </slot>
        </div>
      </Modal>

      <!--   是否删除确认框   -->
      <!--      <Modal v-model="deleteModal" title="是否删除字典类别？">-->
      <!--        <p slot="header" style="color: #ed4014; text-align: center">-->
      <!--          <Icon type="ios-information-circle"></Icon>-->
      <!--          <span>删除字典类别</span>-->
      <!--        </p>-->
      <!--        <p>-->
      <!--          此操作会删除"{{ handleRow.name }}"类别和该类别所有字典数据且<span style="color: #ed4014">不可逆</span-->
      <!--          >，是否删除？-->
      <!--        </p>-->
      <!--        <div slot="footer">-->
      <!--          <Button type="error" size="large" @click="delOk">删除</Button>-->
      <!--          <Button type="default" size="large" @click="delCancel">取消</Button>-->
      <!--        </div>-->
      <!--      </Modal>-->
      <Modal v-model="deleteModal" title="是否删除字典类别？" @on-ok="delOk" @on-cancel="delCancel">
        <p>{{ handleRow.name }}</p>
      </Modal>

      <!--   是否删除字典确认框   -->
      <!--      <Modal v-model="deleteModal2" title="是否删除字典？">-->
      <!--        <p slot="header" style="color: #ed4014; text-align: center">-->
      <!--          <Icon type="ios-information-circle"></Icon>-->
      <!--          <span>删除字典</span>-->
      <!--        </p>-->
      <!--        <p>是否删除"{{ handleRow2.name }}"？</p>-->
      <!--        <div slot="footer">-->
      <!--          <Button type="error" size="large" @click="delOk2">删除</Button>-->
      <!--          <Button type="default" size="large" @click="delCancel">取消</Button>-->
      <!--        </div>-->
      <!--      </Modal>-->
      <Modal v-model="deleteModal2" title="是否删除字典？" @on-ok="delOk2" @on-cancel="delCancel">
        <p>{{ handleRow2.name }}</p>
      </Modal>
    </div>
  </div>
</template>

<script>
const systemDictTypeVO = {
  id: '',
  value: '',
  name: '',
};

const systemDictVO = {
  id: '',
  value: '',
  name: '',
  desc: '',
};

import {
  getListDictType,
  getListDict,
  updateDictType,
  createDictType,
  deleteDictType,
  deleteDict,
  createDict,
  updateDict,
} from './systemDictAPI';

export default {
  name: 'systemDict',
  data() {
    return {
      handleRow: '',
      handleRow2: '',
      keyword: '',
      tableHeight: 400,
      tableWidth: '',
      dataMain: {
        currentRow: {
          id: '',
          name: '',
          value: '',
        },
        currentRow2: {
          id: '',
          name: '',
          value: '',
          desc: '',
          type: '',
        },
        total: 0, //总数
        total2: 0, //表2总数
        index: -1, //当前行的index
        pageNumber: 1, //初始化加载第一页，默认第一页
        pageSize: 10, //初始每页数量
        data: [], //表格数据
        data2: [], //表格2数据
        columns: [
          //表头
          { type: 'index', title: 'No.', width: 60, align: 'center' },
          { title: '编码', key: 'value', minWidth: 120, align: 'center' },
          { title: '名称', key: 'name', minWidth: 140, align: 'center' },
        ],
        columns2: [
          //表头
          { type: 'index', title: 'No.', width: 60, align: 'center' },
          { title: '值', key: 'value', minWidth: 120, align: 'center' },
          { title: '名称', key: 'name', minWidth: 120, align: 'center' },
          { title: '描述', key: 'desc', minWidth: 140, align: 'center' },
        ],
      },
      showDictTypeModal: false,
      showDictModal: false,
      deleteModal: false,
      deleteModal2: false,
      dictTypeTableLoading: false,
      dictTableLoading: false,
      dictTypeModalTitle: '',
      dictModalTitle: '',
      modalType: '',
      // showDetails: false,
      /**验证 **/
      validation_data: {
        value: [{ required: true, message: '编码不能为空', trigger: 'blur' }],
        name: [{ required: true, message: '名称不能为空', trigger: 'blur' }],
      },
      validation_data2: {
        value: [{ required: true, message: '值不能为空', trigger: 'blur' }],
        name: [{ required: true, message: '名称不能为空', trigger: 'blur' }],
      },
    };
  },
  methods: {
    handleRowChange(currentRow) {
      this.handleRow = currentRow;
      this.getTable2Data();
    },
    handleRowChange2(currentRow) {
      this.handleRow2 = currentRow;
    },
    queryList() {
      this.dictTypeTableLoading = true;
      this.handleRow = '';
      this.handleRow2 = '';
      let _this = this;
      let pageData = {
        page: _this.dataMain.pageNumber,
        limit: _this.dataMain.pageSize,
        orderField: '',
        order: 'asc',
      };
      getListDictType(this.keyword, pageData).then(res => {
        if (res.code === 0) {
          this.dataMain.data = res.data.list;
          this.dataMain.total = res.data.total;
          this.dictTypeTableLoading = false;
        } else {
          console.log(res.msg);
        }
      });
      this.getTable2Data();
    },
    showCreateDictTypeMod(str) {
      this.showMod(str);
      this.dictTypeModalTitle = '新增字典类别';
      this.dataMain.currentRow.id = '';
      this.dataMain.currentRow.value = '';
      this.dataMain.currentRow.name = '';
      this.showDictTypeModal = true;
    },
    showUpdateDictTypeMod(str) {
      this.showMod(str);
      if (this.handleRow) {
        this.dictTypeModalTitle = '修改字典类别';
        this.dataMain.currentRow.id = this.handleRow.id;
        this.dataMain.currentRow.value = this.handleRow.value;
        this.dataMain.currentRow.name = this.handleRow.name;
        this.showDictTypeModal = true;
      } else {
        this.$Message.error('未选择要修改的字典类别,请检查');
      }
    },
    showCreateDictMod(str) {
      this.showMod(str);
      if (this.handleRow) {
        this.dictModalTitle = '新增字典';
        this.dataMain.currentRow2.value = '';
        this.dataMain.currentRow2.name = '';
        this.dataMain.currentRow2.desc = '';
        this.dataMain.currentRow2.type = this.handleRow.value;
        this.showDictModal = true;
      } else {
        this.$Message.error('未选择字典类别,请检查');
      }
    },
    showUpdateDictMod(str) {
      this.showMod(str);
      if (this.handleRow2) {
        this.dictModalTitle = '修改字典';
        this.dataMain.currentRow2.id = this.handleRow2.id;
        this.dataMain.currentRow2.value = this.handleRow2.value;
        this.dataMain.currentRow2.name = this.handleRow2.name;
        this.dataMain.currentRow2.desc = this.handleRow2.desc;
        this.dataMain.currentRow2.type = this.handleRow2.value;
        this.showDictModal = true;
      } else {
        this.$Message.error('未选择要修改的字典,请检查');
      }
    },
    showMod(str) {
      this.modalType = str;
    },
    closeMod() {
      this.showDictTypeModal = false;
      this.deleteModal = false;
      this.dataMain.currentRow = JSON.parse(JSON.stringify(systemDictTypeVO));
      this.$refs.create.fields.forEach(v => {
        v.validateDisabled = true;
        v.validateState = '';
        v.validateMessage = '';
      });
    },
    closeMod2() {
      this.showDictModal = false;
      this.deleteModal2 = false;
      this.dataMain.currentRow2 = JSON.parse(JSON.stringify(systemDictVO));
      this.$refs.create2.fields.forEach(v => {
        v.validateDisabled = true;
        v.validateState = '';
        v.validateMessage = '';
      });
    },
    closeFromCorner(e) {
      if (!e) {
        this.closeMod();
      }
    },
    add_success: function () {
      let _modalType = this.modalType;
      let _currentRow = this.dataMain.currentRow;

      this.$refs['create'].validate(valid => {
        if (valid) {
          let dictTypeData = {};
          dictTypeData.value = _currentRow.value;
          dictTypeData.name = _currentRow.name;
          if (_modalType === 'create') {
            createDictType(dictTypeData).then(res => {
              if (res.code === 0) {
                this.showDictTypeModal = false;
                this.$Message.success('新增成功');
                this.queryList();
              } else {
                this.$Message.error('新增失败');
              }
            });
          } else if (_modalType === 'update') {
            dictTypeData.id = _currentRow.id;
            updateDictType(dictTypeData).then(res => {
              if (res.code === 0) {
                this.showDictTypeModal = false;
                this.$Message.success('修改成功');
                this.queryList();
              } else {
                this.$Message.error('修改失败');
              }
            });
          }
        }
      });
    },
    add_success2: function () {
      console.log('submit ' + this.modalType);
      let _modalType = this.modalType;
      let _currentRow2 = this.dataMain.currentRow2;
      console.log(_currentRow2);
      this.$refs['create2'].validate(valid => {
        if (valid) {
          let dictData = {};
          dictData.value = _currentRow2.value;
          dictData.name = _currentRow2.name;
          dictData.desc = _currentRow2.desc;
          dictData.type = _currentRow2.type;
          if (_modalType === 'create2') {
            createDict(dictData).then(res => {
              if (res.code === 0) {
                this.showDictModal = false;
                this.$Message.success('新增成功');
                this.getTable2Data();
              } else {
                this.$Message.error('新增失败');
              }
            });
          } else if (_modalType === 'update2') {
            dictData.id = _currentRow2.id;
            updateDict(dictData).then(res => {
              if (res.code === 0) {
                this.showDictModal = false;
                this.$Message.success('修改成功');
                this.getTable2Data();
              } else {
                this.$Message.error('修改失败');
              }
            });
          }
        }
      });
    },
    showDel() {
      if (this.handleRow) {
        this.deleteModal = true;
      } else {
        this.$Message.error('未选择删除项目,请检查');
      }
    },
    showDel2() {
      if (this.handleRow2) {
        this.deleteModal2 = true;
      } else {
        this.$Message.error('未选择删除项目,请检查');
      }
    },
    delOk() {
      let _handleRow = this.handleRow;
      deleteDictType(_handleRow.value).then(res => {
        if (res.code === 0) {
          this.deleteModal = false;
          this.$Message.success('删除成功');
          this.closeMod();
          this.closeMod2();
          this.queryList();
          this.getTable2Data();
        } else {
          this.$Message.error('删除失败 ' + res.msg);
        }
      });
    },
    delOk2() {
      let _handleRow2 = this.handleRow2;
      deleteDict(_handleRow2.id).then(res => {
        if (res.code === 0) {
          this.deleteModal2 = false;
          this.$Message.success('删除成功');
          this.clearForm2();
          this.closeMod2();
          this.getTable2Data();
        } else {
          this.$Message.error('删除失败 ' + res.msg);
        }
      });
    },
    delCancel() {
      this.deleteModal = false;
      this.deleteModal2 = false;
    },
    pageChangeTable(val) {
      //页码改变
      this.dataMain.pageNumber = val;
      this.queryList();
    },
    pageSizeChange(val) {
      // 页码size改变
      this.dataMain.pageSize = val;
      this.queryList();
    },
    getTable2Data() {
      this.clearForm2();
      this.dictTableLoading = true;
      let _handleRow = this.handleRow;
      let _dataMain = this.dataMain;
      getListDict(_handleRow.value).then(res => {
        if (res.code === 0) {
          _dataMain.data2 = res.data;
          _dataMain.total2 = res.data.length;
          this.dictTableLoading = false;
        }
      });
    },
    clearForm2() {
      this.dataMain.currentRow2.id = '';
      this.dataMain.currentRow2.name = '';
      this.dataMain.currentRow2.value = '';
      this.dataMain.currentRow2.desc = '';
      this.dataMain.currentRow2.type = '';
      this.handleRow2 = '';
    },
  },
  mounted() {
    this.queryList();
    this.tableHeight =
      window.innerHeight -
      this.$refs.table.$el.offsetParent.offsetTop -
      this.$refs.page.$el.clientHeight -
      24 * 2 -
      10 -
      2;
    this.tableWidth = window.innerWidth - this.$refs.table.$el.offsetLeft - 54;
    window.onresize = () => {
      return (() => {
        console.log(
          window.innerHeight - this.$refs.table.$el.offsetParent.offsetTop - this.$refs.page.$el.clientHeight,
        );
        console.log(this.$refs.table.$el.offsetParent.offsetTop);
        this.tableHeight =
          window.innerHeight -
          this.$refs.table.$el.offsetParent.offsetTop -
          this.$refs.page.$el.clientHeight -
          24 * 2 -
          10 -
          2;
        this.tableWidth = window.innerWidth - this.$refs.table.$el.offsetLeft - 50;
      })();
    };
  },
};
</script>

<style scoped>
@import '../../../assets/less/common.css';
</style>
