import server from '@/common/server';

export function getMappingDepartment(keyword, orgId, mapid) {
  return server.get({
    url: 'base/mappingdepartment/getMappingDepartment',
    params: {
      deptName: keyword,
      orgId: orgId, //后台未用到该属性
      type: mapid,
    },
  });
}
//映射数据
export function MappingDepartment(fksid, bzCode, orgId) {
  return server.put({
    url: 'base/mappingdepartment/mappingDepartment',
    params: {
      fksid: fksid,
      fbzksid: bzCode,
      fhid: orgId,
    },
  });
}
export function getMappingDeptBz(keyword, regionId) {
  return server.get({
    url: 'base/deptbz/getDeptBz',
    params: {
      deptName: keyword,
      regionId: regionId,
    },
  });
}
//取消映射数据
export function unmapDepartment(code, bzCode, orgId) {
  return server.put({
    url: 'base/mappingdepartment/unmapDepartment',
    params: {
      fksid: code,
      fbzksid: bzCode,
      fhid: orgId,
    },
  });
}
