<template>
  <div>
    <Row>
      <div class="mainDiv">
        <div style="display: flex">
          <div>
            <Input
              search
              v-model.trim="searchData.keyword"
              @on-search="queryList"
              placeholder="按科室名称搜索"
              style="width: 180px"
              clearable
            />
          </div>
          <div style="display: flex; margin-left: 10px">
            <Select v-model="searchData.mapid" filterable placeholder="请选择">
              <Option v-for="item in searchData.mapList" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
          </div>
        </div>
        <div>
          <Button type="primary" @click="queryList" icon="md-search" style="margin-right: 5px">查询</Button>
        </div>
      </div>
    </Row>
    <Row>
      <!--      <Table-->
      <!--        border-->
      <!--        ref="table"-->
      <!--        row-key="id"-->
      <!--        size="small"-->
      <!--        :height="dataMain.tableHeight"-->
      <!--        :width="dataMain.tableWidth"-->
      <!--        :columns="dataMain.columns"-->
      <!--        highlight-row-->
      <!--        :_showChildren="false"-->
      <!--        :loading="dataMain.loading"-->
      <!--        :data="testData"-->
      <!--        :load-data="handleLoadData"-->
      <!--        tooltip-theme="light"-->
      <!--      >-->
      <!--      </Table>-->
      <el-table
        :data="dataMain.data"
        size="small"
        style="width: 100%; margin-bottom: 20px"
        row-key="id"
        border
        lazy
        height="35vw"
        ref="deptTable"
        :load="load"
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
        :loading="dataMain.loading"
      >
        <el-table-column prop="hisName" label="科室名称" align="center" min-width="180"></el-table-column>
        <el-table-column prop="hisCode" label="科室编码" align="center" min-width="180"></el-table-column>
        <el-table-column prop="bzName" label="标准科室名称" align="center" min-width="180"></el-table-column>
        <el-table-column prop="bzCode" label="标准科室编码" align="center" min-width="180"></el-table-column>
        <el-table-column label="操作" align="center" min-width="180">
          <template slot-scope="scope">
            <el-button
              v-if="dataMain.mapOrNot"
              style="background-color: #2a378f;color: white; border-radius：10px"
              size="mini"
              @click="setMap(scope)"
            >
              映射
            </el-button>
            <el-button
              v-else
              size="mini"
              style="background-color: #2a378f;color: white; border-radius：10px"
              @click="cancelMap(scope)"
            >
              取消映射
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </Row>
    <Modal
      draggable1
      mask
      :mask-closable="false"
      v-model="modalData.modaldetail"
      width="1000px"
      :footerHide="true"
      :styles="{ top: '60px' }"
    >
      <span slot="header" v-html="modalData.detailText"></span>
      <Row>
        <div style="display: flex; justify-content: space-between; width: 100%; margin-bottom: 10px">
          <div style="display: flex">
            <div>
              <Input
                search
                v-model.trim="searchData.keywordDetailed"
                @on-search="queryDetailedList"
                placeholder="按标准科室名称搜索"
                style="width: 180px"
                clearable
              />
            </div>
          </div>
          <div>
            <Button type="primary" @click="queryDetailedList" icon="md-search" style="margin-right: 5px">查询</Button>
          </div>
        </div>
      </Row>
      <Table
        border
        ref="table"
        row-key="id"
        size="small"
        :height="dataMain.detailedTableHeight"
        :width="dataMain.tableWidth"
        :columns="dataMain.detailedColumns"
        highlight-row
        @on-current-change="handleRowChangeDetailed"
        :data="dataMain.dataDetailed"
        tooltip-theme="light"
        :loading="dataMain.loadingDetailed"
      >
      </Table>
      <div class="ivu-modal-footer">
        <slot name="footer">
          <Button @click.native="modalData.modaldetail = false">取 消</Button>
          <Button type="primary" @click="save">确 定</Button>
        </slot>
      </div>
    </Modal>
  </div>
</template>
<!--科室映射页面-->
<script>
import { getMappingDepartment, MappingDepartment, getMappingDeptBz, unmapDepartment } from './departmentMappingAPI';

export default {
  name: 'departmentMapping',
  data() {
    return {
      //模态框数据
      modalData: {
        detailText: '',
        modaldetail: false,
      },
      //查询数据
      searchData: {
        keyword: '',
        keywordDetailed: '',
        mapid: 0,
        fdeptid: '',
        mapList: [
          {
            id: 0,
            name: '未映射',
          },
          {
            id: 1,
            name: '映射',
          },
        ],
      },
      //table数据
      dataMain: {
        mapOrNot: true,
        columns: [
          {
            title: 'No.',
            width: 160,
            align: 'lift',
            tree: true,
            className: 'table_cell_align_left',
            render: (h, params) => {
              return h('span', params.index + 1);
            },
          },
          { title: '科室名称', key: 'hisName', minWidth: 100, align: 'center' },
          { title: '科室编码', key: 'hisCode', minWidth: 100, align: 'center' },
          { title: '标准科室名称', key: 'bzName', minWidth: 140, align: 'center' },
          { title: '标准科室编码', key: 'bzCode', minWidth: 140, align: 'center' },
          {
            title: '操作',
            key: 'parentTitle',
            minWidth: 100,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h(
                  'i-button',
                  {
                    props: {
                      type: 'primary',
                      size: 'small',
                    },
                    style: {
                      marginRight: '5px',
                    },
                    on: {
                      click: () => {
                        this.dataMain.mapOrNot === true ? this.setMap(params) : this.cancelMap(params);
                      },
                    },
                  },
                  this.dataMain.mapOrNot === true ? '映射' : '取消映射',
                ),
              ]);
            },
          },
        ],
        data: [],
        detailedColumns: [
          {
            title: 'No.',
            width: 80,
            align: 'lift',
            tree: true,
            className: 'table_cell_align_left',
            render: (h, params) => {
              return h('span', params.index + 1);
            },
          },
          // { title: '上级科室名称', key: 'superdeptname', minWidth: 140, align: 'center' },
          // { title: '上级科室编码', key: 'superdeptcode', minWidth: 140, align: 'center' },
          { title: '标准科室名称', key: 'deptName', minWidth: 140, align: 'center' },
          { title: '标准科室编码', key: 'deptCode', minWidth: 140, align: 'center' },
        ],
        dataDetailed: [],
        detailRow: [],
        regionList: [],
        tableHeight: window.innerHeight - 220,
        detailedTableHeight: window.innerHeight - 320,
        tableWidth: '',
        loading: false,
        loadingDetailed: false,
      },
      testData: [],
    };
  },
  methods: {
    load(row, treeNode, resolve) {
      resolve(row.children);
    },
    handleLoadData(row, treeNode, resolve) {
      console.log(row);
      let index = this.dataMain.data.findIndex(v => {
        return v.id === row.id;
      });
      resolve(this.dataMain.data[index].children);
    },
    queryList() {
      let _this = this;
      let keyword = _this.searchData.keyword;
      let mapid = _this.searchData.mapid;
      let orgId = 1;
      this.dataMain.mapOrNot = _this.searchData.mapid !== 1; //设置按钮 如果页面选择未映射  按钮就是映射
      this.dataMain.loading = true; // 如果页面选择映射  按钮就是取消映射
      getMappingDepartment(keyword, orgId, mapid).then(res => {
        if (res.data) {
          this.dataMain.data = res.data;
          this.testData = [];
          let arr = JSON.parse(JSON.stringify(res.data));
          arr.forEach(v => {
            v.hasChildren = true;
            this.testData.push(v);
          });
          console.log(this.testData);
          this.dataMain.loading = false;
        }
      });
    },
    queryDetailedList() {
      let keyword = this.searchData.keywordDetailed;
      this.dataMain.loadingDetailed = true;
      getMappingDeptBz(keyword).then(res => {
        if (res.data) {
          this.dataMain.dataDetailed = res.data;
          this.dataMain.loadingDetailed = false;
        }
      });
    },
    setMap(parmas) {
      this.searchData.fdeptid = parmas.row.id;
      this.modalData.detailText =
        '科室编码：' + parmas.row.hisCode + '&nbsp&nbsp&nbsp&nbsp&nbsp科室名称：' + parmas.row.hisName;
      this.queryDetailedList();
      this.modalData.modaldetail = true;
    },
    save() {
      if (this.dataMain.detailRow.length === 0) {
        return this.$Message.error('请选择要映射的数据');
      }
      let fksid = this.searchData.fdeptid; //科室id
      let bzCode = this.dataMain.detailRow.fid;
      let orgId = 1;
      MappingDepartment(fksid, bzCode, orgId).then(res => {
        if (res.code === 0) {
          this.$Message.success(res.msg);
          this.modalData.modaldetail = false;
          this.dataMain.detailRow = [];
          this.queryList();
        } else {
          this.$Message.error(res.msg);
        }
      });
    },
    cancelMap(parmas) {
      unmapDepartment(parmas.row.id).then(res => {
        if (res.code === 0) {
          this.$Message.success(res.msg);
          this.dataMain.detailRow = [];
          this.queryList();
        } else {
          this.$Message.error(res.msg);
        }
      });
    },
    handleRowChangeDetailed(currentRow) {
      this.dataMain.detailRow = currentRow;
    },
  },
  mounted() {
    this.dataMain.tableHeight = window.innerHeight - 220;
    this.dataMain.detailedTableHeight = window.innerHeight - 320;
    this.queryList();
  },
};
</script>

<style scoped>
.mainDiv {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 10px;
}
</style>
