import server from '@/common/server';
/**
 * 查询所有药店(分页)
 * @param storeName
 * @param pageData
 * @returns {*}
 */
export function getAllStoreList(storeName, pageData) {
  return server.get({
    url: '/drug/firm/getAllStoreList',
    params: {
      storeName: storeName,
      'pageData.limit': pageData.limit,
      'pageData.page': pageData.page,
      'pageData.order': 'asc',
      'pageData.orderField': '',
    },
  });
}
/**
 * 查询所有药店除去已映射的(分页)
 * @param storeName
 * @param pageData
 * @returns {*}
 */
export function getAllByIdStoreList(storeName, pageData) {
  return server.get({
    url: '/drug/firm/getAllByIdStoreList',
    params: {
      storeName: storeName,
      'pageData.limit': pageData.limit,
      'pageData.page': pageData.page,
      'pageData.order': 'asc',
      'pageData.orderField': '',
    },
  });
}
