import server from '@/common/server';
// 获取oss上传签名
export function getPolicy() {
  return server.get({
    url: '/aliyun/oss/policy',
  });
}

// 获取已经上传的文件列表
export function getUploadFileList(tableName, tableColumn, tableId) {
  return server.get({
    url: '/aliyun/oss/getUploadFileList',
    params: {
      tableName,
      tableColumn,
      tableId,
    },
  });
}

// 修改文件路径
export function insertFileUrl(tableName, tableColumn, tableId, fileUrl, createTime) {
  return server.put({
    url: '/aliyun/oss/insertFileUrl',
    params: {
      tableName,
      tableColumn,
      tableId,
      fileUrl,
      createTime,
    },
  });
}

// 修改文件路径
export function deleteFileUrl(fileUrl) {
  return server.del({
    url: '/aliyun/oss/deleteFileUrl',
    params: {
      fileUrl,
    },
  });
}
