<template>
  <div>
    <div v-if="img" class="upload">
      <img :src="getImgUrl()" style="width: 58px; height: 58px" />
      <div class="upload-cover">
        <Icon type="ios-eye-outline" @click.native="handleView"></Icon>
        <Icon type="ios-trash-outline" @click="handleRemove"></Icon>
      </div>
    </div>
    <div v-if="!img" class="demo-spin-container">
      <Upload
        :data="dataObj"
        :action="uploadAction"
        ref="upload"
        name="file"
        type="drag"
        :show-upload-list="false"
        :on-success="handleSuccess"
        :format="['jpg', 'jpeg', 'png']"
        :max-size="5120"
        :on-format-error="handleFormatError"
        :on-exceeded-size="handleMaxSize"
        :on-remove="handleRemove"
        :on-error="handleError"
        :before-upload="handleBeforeUpload"
        style="display: inline-block; width: 58px"
      >
        <div style="width: 58px; height: 58px; line-height: 58px">
          <Icon type="ios-camera" size="20"></Icon>
        </div>
      </Upload>
      <Spin v-if="spinShow" fix></Spin>
    </div>

    <Modal title="查看图片" v-model="visible" footer-hide>
      <img :src="getImgUrl()" style="width: 100%" />
    </Modal>
  </div>
</template>

<script>
import { getPolicy } from './uploadImage';
import context from '@/admin-pc/utils/context';
export default {
  name: 'uploadImage',
  props: {
    image: String,
  },
  watch: {
    image: {
      handler(val) {
        if (val === null) {
          this.img = false;
          return;
        }
        this.img = val;
      },
    },
  },
  data() {
    return {
      uploadAction: context.uploadAction,
      visible: false,
      spinShow: false,
      img: '',
      dataObj: {
        policy: '',
        Signature: '',
        key: '',
        OSSAccessKeyId: '',
        dir: '',
        host: '',
        callback: '',
      },
      dataList: [],
      uploadList: [],
      xmlParser: new DOMParser(),
      fileVisible: false,
      fileTitle: '',
      fileUrl: '',
    };
  },

  methods: {
    getImgUrl() {
      //返回img url
      let url = '';
      if (this.img) {
        url = context.uploadAction.toString() + `/` + this.img.toString();
      }
      return url;
    },
    handleSuccess(res, file) {
      if (res.code === 0) {
        this.img = res.data.filename;
        this.$emit('filename', res.data.filename);
      } else {
        let result = this.parserXml(res);
        this.$Notice.warning({
          title: '文件返回访问地址失败',
          desc: '文件：' + file.name + '<br>' + result.code + '<br>' + result.message,
        });
      }
      this.spinShow = false;
    },
    handleView() {
      this.visible = true;
    },
    handleRemove() {
      this.img = '';
      this.$emit('filename', '');
    },
    handleBeforeUpload(file) {
      return new Promise((resolve, reject) => {
        this.spinShow = true;
        getPolicy()
          .then(res => {
            if (res.code === 0) {
              this.dataObj.policy = res.data.policy;
              this.dataObj.Signature = res.data.signature;
              this.dataObj.OSSAccessKeyId = res.data.accessKeyId;
              this.dataObj.key = res.data.dir + '/${filename}';
              this.dataObj.dir = res.data.dir;
              this.dataObj.host = res.data.host;
              this.dataObj.callback = res.data.callback;
              resolve(true);
            } else {
              this.$Notice.warning({
                title: file.name + '文件获取上传签名失败',
              });
              reject(false);
            }
          })
          .catch(err => {
            console.log(err);
            reject(false);
          });
      });
    },
    handleError(error, file) {
      this.spinShow = false;
      let res = this.parserXml(file);
      this.$Notice.warning({
        title: '文件上传失败',
        desc: res.code + '<br>' + res.message,
      });
      this.spinShow = false;
    },
    handleMaxSize(file) {
      this.$Notice.warning({
        title: '超出文件大小限制！',
        desc: file.name + '文件大小超过了2M(兆)',
      });
      this.spinShow = false;
    },
    handleFormatError(file) {
      this.$Notice.warning({
        title: '文件格式不正确！',
        desc: file.name + ' 格式不正确, 请选择jpg，jpeg，png，bmp，gif，webp，tiff',
      });
      this.spinShow = false;
    },
    /************************************** 转化xml对象，提取code和message节点的内容 ******************************************************/
    parserXml(xmlStr) {
      let xmlDoc = this.xmlParser.parseFromString(xmlStr, 'text/xml');
      let code = xmlDoc.firstChild.childNodes;
      let result = {
        code: code[1].innerHTML,
        message: code[3].innerHTML,
      };
      return result;
    },
    /**************************************获取当前时间 格式：yyyy-MM-dd HH:MM:SS ******************************************************/
    getCurrentTime() {
      let date = new Date(); //当前时间
      let month = this.zeroFill(date.getMonth() + 1); //月
      let day = this.zeroFill(date.getDate()); //日
      let hour = this.zeroFill(date.getHours()); //时
      let minute = this.zeroFill(date.getMinutes()); //分
      let second = this.zeroFill(date.getSeconds()); //秒
      //当前时间
      let curTime = date.getFullYear() + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second;
      return curTime;
    },
    /**************************************  补零 ******************************************************/
    zeroFill(i) {
      if (i <= 9 && i >= 0) {
        return '0' + i;
      } else {
        return i;
      }
    },
  },
};
</script>

<style scoped>
.upload {
  display: inline-block;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  margin-right: 4px;
}
.upload-cover {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
}

.upload:hover .upload-cover {
  display: block;
}

.upload-cover i {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin: 0 2px;
}
.demo-spin-container {
  display: inline-block;
  position: relative;
  height: 60px;
  border: 1px solid #eee;
}
</style>
