<template>
  <div>
    <div style="display: flex; justify-content: space-between; margin-bottom: 10px">
      <Input
        search
        v-model.trim="methodKeyword"
        @on-search="queryMethodList"
        clearable
        placeholder="按药店名搜索"
        style="width: 180px"
      ></Input>
      <Button type="primary" @click="queryMethodList" icon="md-search" style="margin-right: 5px">查询</Button>
    </div>
    <div>
      <Table
        border
        ref="methodTable"
        size="small"
        :columns="dataMain.methodColumns"
        highlight-row
        @on-current-change="methodHandleRowChange"
        :data="dataMain.methodData.data"
        tooltip-theme="light"
        :loading="modal.methodTableLoading"
      >
      </Table>
      <Page
        ref="page"
        style="margin-top: 10px; margin-bottom: 10px"
        align="right"
        :total="dataMain.methodData.total"
        :current="dataMain.methodData.pageNumber"
        :page-size="dataMain.methodData.pageSize"
        :page-size-opts="[10, 20, 50, 100]"
        show-elevator
        show-total
        show-sizer
        @on-change="pageChangeMethodTable"
        @on-page-size-change="pageSizeChangeMethodTable"
      />
    </div>
    <div class="ivu-modal-footer">
      <slot name="footer">
        <Button @click="closeMethodTableModal">取 消</Button>
        <Button type="primary" @click="ok">确 定</Button>
      </slot>
    </div>
  </div>
</template>
<!--所有药店弹出框-->
<script>
import { getAllStoreList } from './drugstoreTable';
export default {
  name: 'drugstoreAllTable',
  data() {
    return {
      showMethodTableModal: false,
      methodKeyword: '',
      dataMain: {
        methodData: {
          total: 0, //总数
          pageNumber: 1, //初始化加载第一页，默认第一页
          pageSize: 10, //初始每页数量
          data: [], //表格数据
        },
        methodColumns: [
          { type: 'index', title: 'No.', width: 60, align: 'center' },
          { title: '药店名称', key: 'name', minWidth: 140, align: 'center' },
        ],
      },
      modal: {
        methodTableLoading: false,
      },
      methodHandleRow: '',
    };
  },
  methods: {
    queryMethodList() {
      this.modal.methodTableLoading = true;
      let pageData = {
        limit: this.dataMain.methodData.pageSize,
        page: this.dataMain.methodData.pageNumber,
      };
      this.dataMain.data = [];
      getAllStoreList(this.methodKeyword, pageData).then(res => {
        if (res.code === 0) {
          this.dataMain.methodData.data = res.data.list;
          this.dataMain.methodData.total = res.data.total;
        }
        this.modal.methodTableLoading = false;
      });
    },
    pageSizeChangeMethodTable(val) {
      // 页码size改变
      this.dataMain.configData.pageSize = val;
      this.queryList();
    },
    pageChangeMethodTable(val) {
      //页码改变
      this.dataMain.methodData.pageNumber = val;
      this.queryMethodList();
    },
    methodHandleRowChange(currentRow) {
      this.methodHandleRow = currentRow;
    },
    closeMethodTableModal() {
      this.$emit('closeDrugStoreModal', '');
    },
    ok() {
      this.$emit('item', this.methodHandleRow);
      this.methodHandleRow = '';
    },
  },
};
</script>
<style scoped>
@import '../../../assets/less/common.css';
</style>
