<template>
    <!--科室收入对比分析-->
    <div>
        <Row>
            <div class="mainDiv">
                <div style="display: flex">
                    <div>
                      <!--  <DatePicker type="month" v-if="showSelect"  :clearable="false" :value="valueTime" placeholder="请选择日期" @on-change="changeMainData" style="width: 200px; margin-right: 10px"></DatePicker><br>-->

                        <DatePicker type="month" v-if="showSelect" :clearable="false" :value="valueTime1"  placeholder="请选择开始日期" @on-change="changeMainData1" :options="startMonthOptions" style="width: 200px; margin-right: 10px"></DatePicker>
                        <DatePicker type="month" v-if="showSelect" :clearable="false" :value="valueTime2"  placeholder="请选择结束日期" @on-change="changeMainData2" :options="endMonthOptions" style="width: 200px; margin-right: 10px"></DatePicker>

                        <!--<Button type="primary" @click="updateDateRange" icon="md-repeat" style="margin-right: 10px">切换</Button>-->


                    </div>
                </div>
                <div style="display: flex">
                    <Button type="primary" @click="queryList" icon="md-search" style="margin-right: 5px">查询</Button>
                     <Button type="primary" icon="ios-log-out" @click="exportData" style="margin-right: 5px" :loading="exportLoad">导出</Button>
                </div>
            </div>
        </Row>
        <Row>
            <div style="width: 100%">
                <Table
                        stripe
                        border
                        ref="table"
                        row-key="id"
                        size="small"
                        :height="dataMain.tableHeight"
                        :width="dataMain.tableWidth"
                        :columns="dataMain.columns"
                        highlight-row
                        :loading="dataMain.loading"
                        :data="dataMain.data"
                />

            </div>
        </Row>
    </div>
</template>

<script>
  import dayjs from 'dayjs';
  import {downloadTemp,getDeptIncomeDate} from './deptIncomeContrastiveAnalysis.js';
  export default {
    name: 'deptIncomeContrastiveAnalysis',
    data(){
      return{
        exportLoad:false,
        //控制显示隐藏
        showSelect:true,
        valueTime:'',
        valueTime1:'',
        valueTime2:'',
        queryData: {
          beginDate: '',
          endDate: '',
        },
        // 限制可选的开始年月
        startMonthOptions: {
          disabledDate: date => {
            if (this.queryData.endDate !=='') {
              let endDate = this.getDate(this.queryData.endDate);
              return (date && date > endDate) || date > new Date();
            } else {
              return date && date > new Date();
            }
          }
        },
        // 限制可选的结束年月
        endMonthOptions: {
          disabledDate: date => {
            if (this.queryData.beginDate !=='') {
              let beginDate = this.getDate(this.queryData.beginDate);
              return (date && date < beginDate) || date > new Date();
            } else {
              return date && date > new Date();
            }
          }
        },
        dataMain: {
          columns: [],
          data: [],
          tableWidth: '',
          tableHeight: window.innerHeight  - 220,
          loading: false,
          handleRow: [],
        }
      }
    },
    methods:{
      // 主表格查询
      queryList(){
        //判断是否查询
        var start =  this.queryData.beginDate.split('-');
        var end =  this.queryData.endDate.split('-');
        if(this.queryData.beginDate.split('-')[0] !=  this.queryData.endDate.split('-')[0] ){
          this.$Message.error("请不要跨年查询");
          return;
        }
        this.changeColumns();
        this.dataMain.loading = true;
        getDeptIncomeDate('',this.queryData.beginDate,this.queryData.endDate).then((res)=>{
          if (res.data) {
            this.dataMain.data = res.data.list;
            this.dataMain.loading = false;
          }else{
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 动态修改表头
      changeColumns(){
        var start =  this.queryData.beginDate.split('-');
        var end =  this.queryData.endDate.split('-');

        //去掉零值
        if("0" == start[1].substring(0, 1)){
          start[1] = start[1].substring(1);
        }
        if("0" ==end[1].substring(0, 1)){
          end[1] = end[1].substring(1);
        }

        // 今年
        var jinnian = start[0];
        // 去年
        var qunian = start[0]-1;
        // 前
        var qiannian = start[0]-2;
        if(start[1] == end[1]){
          jinnian = jinnian +'年'+start[1]+'月'
          qunian = qunian +'年'+start[1]+'月'
            qiannian = qiannian +'年'+start[1]+'月'
        }else{
          jinnian = jinnian +'年'+start[1]+'-'+end[1]+'月'
          qunian = qunian +'年'+start[1]+'-'+end[1]+'月'
          qiannian = qiannian +'年'+start[1]+'-'+end[1]+'月'
        }

        this.dataMain.columns =[

          {
            title: 'No.',
            fixed: 'left' ,
            width: 60,
            align: 'left',
            render: (h, params) => {
              return h('span', params.index + 1);
            },
          },
          { title: '科室', key: '科室', minWidth: 120, align: 'center',fixed: 'left' },
          {
            title: '毛收入（含药品、卫材）',
            align: 'center',
            children: [
              { title: jinnian, key: '毛收入', minWidth: 120, align: 'center' },
              { title: qunian, key: '去年毛收入', minWidth: 120, align: 'center' },
              { title: '增减额', key: '毛收入去年增减额', minWidth: 120, align: 'center' },
              { title: '增减比', key: '毛收入去年增减比', minWidth: 90, align: 'center' },
              { title:qiannian, key: '前年毛收入', minWidth: 120, align: 'center' },
              { title: '增减额', key: '毛收入前年增减额', minWidth: 120, align: 'center' },
              { title: '增减比', key: '毛收入前年增减比', minWidth: 90, align: 'center' },
            ],
          },
          {
            title: '有效收入（不含药品、卫材）',
            align: 'center',
            children: [
              { title: jinnian, key: '有效收入', minWidth: 120, align: 'center' },
              { title: qunian, key: '去年有效收入', minWidth: 120, align: 'center' },
              { title: '增减额', key: '有效去年增减额', minWidth: 120, align: 'center' },
              { title: '增减比', key: '有效去年增减比', minWidth: 90, align: 'center' },
              { title: qiannian, key: '前年有效收入', minWidth: 120, align: 'center' },
              { title: '增减额', key: '有效前年增减额', minWidth: 120, align: 'center' },
              { title: '增减比', key: '有效前年增减比', minWidth: 90, align: 'center' },
            ],
          },
          {
            title: '药占比',
            align: 'center',
            children: [
              { title: jinnian, key: '药占比', minWidth: 120, align: 'center' },
              { title: qunian, key: '去年药占比', minWidth: 120, align: 'center' },
              { title: '增减比', key: '药品去年增减比', minWidth: 90, align: 'center' },
              { title: qiannian, key: '前年药占比', minWidth: 120, align: 'center' },
              { title: '增减比', key: '药品前年增减比', minWidth: 90, align: 'center' },
            ],
          },
          {
            title: '门诊均次费用',
            align: 'center',
            children: [
              { title: jinnian, key: '门诊均次费用', minWidth: 120, align: 'center' },
              { title: qunian , key: '去年门诊均次费用', minWidth: 120, align: 'center' },
              { title: '增减额', key: '去年门诊增减额', minWidth: 120, align: 'center' },
              { title: '增减比', key: '去年门诊增减比', minWidth: 90, align: 'center' },
              { title: qiannian , key: '前年门诊均次费用', minWidth: 120, align: 'center' },
              { title: '增减额', key: '前年门诊增减额', minWidth: 120, align: 'center' },
              { title: '增减比', key: '前年门诊增减比', minWidth: 90, align: 'center' },
            ],
          },
          {
            title: '住院均次费用',
            align: 'center',
            children: [
              { title: jinnian, key: '住院均次费用', minWidth: 120, align: 'center' },
              { title: qunian, key: '去年住院均次费用', minWidth: 120, align: 'center' },
              { title: '增减额', key: '去年住院增减额', minWidth: 120, align: 'center' },
              { title: '增减比', key: '去年住院增减比', minWidth: 90, align: 'center' },
              { title: qiannian, key: '前年住院均次费用', minWidth: 120, align: 'center' },
              { title: '增减额', key: '前年住院增减额', minWidth: 120, align: 'center' },
              { title: '增减比', key: '前年住院增减比', minWidth: 90, align: 'center' },
            ],
          },
          {
            title: '卫材占比',
            align: 'center',
            children: [
              { title: jinnian, key: '卫材占比', minWidth: 120, align: 'center' },
              { title: qunian, key: '去年卫材占比', minWidth: 120, align: 'center' },
              { title: '增减比', key: '卫材去年增减比', minWidth: 90, align: 'center' },
              { title: qiannian, key: '前年卫材占比', minWidth: 120, align: 'center' },
              { title: '增减比', key: '卫材前年增减比', minWidth: 90, align: 'center' }
            ],
          }

        ]
      },
      exportData(){
        this.exportLoad = true;
        if(this.queryData.beginDate =='' && this.queryData.beginDate ==''){
          this.$Message.error("请选择时间区间")
          this.exportLoad = false;
          return;
        }

        downloadTemp('导出',this.queryData.beginDate,this.queryData.endDate).then((res=>{
          this.exportLoad = false;
        }));

      },
      //选中主页面日期
      changeMainData(date){
        this.queryData.beginDate = date+'-01';
        var lastDay= new Date(date.split("-")[0],date.split("-")[1],0);

        this.queryData.endDate = this.timeStamp2String(lastDay);
      },
      changeMainData1(date){
        this.queryData.beginDate = date+'-01';
      },
      changeMainData2(date){
        var lastDay= new Date(date.split("-")[0],date.split("-")[1],0);

        this.queryData.endDate = this.timeStamp2String(lastDay);
      },
      //修改查询方式并且重置数据
      updateDateRange(){
        this.showSelect = !this.showSelect;
        this.valueTime='';
        this.valueTime1='';
        this.valueTime2='';
       this.queryData.beginDate ='';
       this.queryData.endDate = '';

      },
      //转化时间字符串
      timeStamp2String(time){
            var datetime = new Date();
            datetime.setTime(time);
            var year = datetime.getFullYear();
            var month = datetime.getMonth() + 1 < 10 ? "0" + (datetime.getMonth() + 1) : datetime.getMonth() + 1;
            var date = datetime.getDate() < 10 ? "0" + datetime.getDate() : datetime.getDate();
            //var hour = datetime.getHours()< 10 ? "0" + datetime.getHours() : datetime.getHours();
           // var minute = datetime.getMinutes()< 10 ? "0" + datetime.getMinutes() : datetime.getMinutes();
            //var second = datetime.getSeconds()< 10 ? "0" + datetime.getSeconds() : datetime.getSeconds();
            return year + "-" + month + "-" + date;
      },
      // 字符串转日期
      getDate(strDate) {
        let monthStr = strDate.split("-")[1];
        let newMonthStr =  parseInt(monthStr)+"";
        strDate = strDate.split("-")[0]+"-"+ newMonthStr +"-1";
        var date = eval(
          "new Date(" +
          strDate
            .replace(/\d+(?=-[^-]+$)/, function(a) {
              return parseInt(a, 10) - 1;
            })
            .match(/\d+/g) +
          ")"
        );
        return date;
      },
      //获取时间
      getFirstTime(){

        var date = this.timeStamp2String(new Date());

        var firstDay= new Date().getFullYear() + '-01-01';

        var lastDay= new Date(date.split("-")[0],date.split("-")[1],0);

        this.valueTime1 = firstDay;
        this.valueTime2 = this.timeStamp2String(lastDay);

        this.queryData.beginDate = firstDay;
        this.queryData.endDate = this.timeStamp2String(lastDay);
      }

    },
    mounted(){
      window.onresize = () => {
        return (() => {
          this.dataMain.tableHeight = window.innerHeight  - 220 //this.$refs.table.$el.offsetTop ;
        })();
      };
    },created(){
        //获取时间
        this.getFirstTime();
        this.queryList();
      window.onresize = () => {
        return (() => {
          this.dataMain.tableHeight = window.innerHeight  - 220  // this.$refs.table.$el.offsetTop -45 ;
        })();
      };
    }
  };
</script>

<style scoped>
    .mainDiv {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 10px;
    }
</style>
