<template>
  <div>
    <!--<Button type="primary" @click="checkUploadList">查看文件列表信息</Button>-->
    <Upload
      ref="upload"
      multiple
      type="drag"
      :data="dataObj"
      :show-upload-list="false"
      :max-size="2048"
      :on-exceeded-size="handleMaxSize"
      :format="['jpg', 'jpeg', 'png', 'bmp', 'gif', 'webp', 'tiff']"
      :on-format-error="handleFormatError"
      :before-upload="handleBeforeUpload"
      :on-remove="handleRemove"
      :on-success="handleSuccess"
      :on-error="handleError"
      action="http://shibo-oss.oss-cn-beijing.aliyuncs.com"
    >
      <div style="padding: 20px 0">
        <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
        <p>点击或拖拽文件到此处进行上传</p>
      </div>
    </Upload>
    <ul style="margin-top: 10px">
      <li class="upload-list" v-for="item in uploadList" :key="item.url">
        <template v-if="item.status === 'finished'">
          <span>
            <Icon type="ios-image" />
            {{ item.name }}
          </span>
          <Poptip class="upload-list-a" confirm placement="left" title="请确认是否删除？" @on-ok="handleRemove(item)">
            <a href="#" style="text-decoration: underline">删除</a>
          </Poptip>
          <a href="#" class="upload-list-a" style="margin-right: 5px" @click="handleView(item)">预览</a>
          <span class="upload-list-span">上传时间：{{ item.time }}</span>
          <!-- <div class="demo-upload-list-cover">
                         <Icon type="ios-eye-outline" @click.native="handleView(item.name)"></Icon>
                         <Icon type="ios-trash-outline" @click.native="handleRemove(item)"></Icon>
                     </div>-->
        </template>
        <template v-else>
          <Progress v-if="item.showProgress" :percent="item.percentage" hide-info :stroke-width="2"></Progress>
        </template>
      </li>
    </ul>
    <Modal v-model="fileVisible" :title="fileTitle" :mask-closable="false" :footerHide="true" width="600px">
      <div style="text-align: center">
        <img :src="fileUrl" v-if="fileVisible" style="max-width: 100%; max-height: 100%" />
      </div>
      <div class="ivu-modal-footer" :v-if="true">
        <slot name="footer">
          <Button type="text" @click.native="fileVisible = false">取消</Button>
          <Button type="primary" @click.native="fileVisible = false">确 定</Button>
        </slot>
      </div>
    </Modal>
  </div>
</template>
<script>
import { getPolicy, getUploadFileList, insertFileUrl, deleteFileUrl } from './uploadFile';
export default {
  name: 'hospitalUpload',
  props: {
    tableName: String, //上传的表名
    tableColumn: String, // 表中的字段
    tableId: Number, // 主键id
    uploadSwitch: Boolean,
  },
  data() {
    return {
      dataObj: {
        policy: '',
        Signature: '',
        key: '',
        OSSAccessKeyId: '',
        dir: '',
        host: '',
        callback: '',
      },
      dataList: [],
      uploadList: [],
      xmlParser: new DOMParser(),
      fileVisible: false,
      fileTitle: '',
      fileUrl: '',
    };
  },
  watch: {
    uploadSwitch() {
      this.searchUploadFileList();
    },
  },
  methods: {
    searchUploadFileList() {
      this.uploadList.length = 0;
      getUploadFileList(this.tableName, this.tableColumn, this.tableId).then(res => {
        if (res.code === 0) {
          let fileList = res.data;
          for (let i = 0, len = fileList.length; i < len; i++) {
            let list = fileList[i].fileUrl.split('/');
            this.uploadList.push({
              url: fileList[i].fileUrl,
              name: list[list.length - 1],
              time: fileList[i].createTime,
              status: 'finished',
            });
          }
          this.$Message.success('查询文件列表成功');
        } else {
          this.$Message.error('查询文件列表失败');
        }
      });
    },
    handleBeforeUpload(file) {
      /*const check = this.uploadList.length < 6; // 这个方法不行，如果已经上传了四张图片，再次上传同时上传3张图片这是拦不住的
                console.log('上传限制最多5张照片：当时长度'+this.uploadList.length)
                if (!check) {
                    this.$Notice.warning({
                        title: '最多能上传5张图片'
                    });
                    return check;
                }*/
      return new Promise((resolve, reject) => {
        getPolicy()
          .then(res => {
            if (res.code === 0) {
              this.dataObj.policy = res.data.policy;
              this.dataObj.Signature = res.data.signature;
              this.dataObj.OSSAccessKeyId = res.data.accessKeyId;
              this.dataObj.key = res.data.dir + '/${filename}';
              this.dataObj.dir = res.data.dir;
              this.dataObj.host = res.data.host;
              this.dataObj.callback = res.data.callback;
              resolve(true);
            } else {
              this.$Notice.warning({
                title: file.name + '文件获取上传签名失败',
              });
              reject(false);
            }
          })
          .catch(err => {
            console.log(err);
            reject(false);
          });
      });
    },
    handleFormatError(file) {
      this.$Notice.warning({
        title: '文件格式不正确！',
        desc: file.name + ' 格式不正确, 请选择jpg，jpeg，png，bmp，gif，webp，tiff',
      });
    },
    handleMaxSize(file) {
      this.$Notice.warning({
        title: '超出文件大小限制！',
        desc: file.name + '文件大小超过了2M(兆)',
      });
    },
    handleRemove(item) {
      deleteFileUrl(item.url).then(res => {
        if (res.code === 0) {
          const fileList = this.$refs.upload.fileList;
          for (let i = 0, len = fileList.length; i < len; i++) {
            // url是文件的唯一标识，name不是(可能出现同名文件)
            if (item.url === fileList[i].url) {
              fileList.splice(i, 1);
              break;
            }
          }
          this.$Message.success('删除成功');
          // 暂未同步删除云上图片
          console.log(fileList);
        } else {
          this.$Message.error('删除文件失败');
        }
      });
    },
    handleView(item) {
      this.fileTitle = item.name;
      this.fileUrl = this.$my.fileHost + item.url;
      this.fileVisible = true;
    },
    handleSuccess(res, file) {
      if (res.code === 0) {
        let fileUrl = res.data.filename;
        file.url = fileUrl;
        file.time = this.getCurrentTime(); // 上传时间
        insertFileUrl(this.tableName, this.tableColumn, this.tableId, fileUrl, file.time).then(res => {
          if (res.code === 0) {
            this.$Notice.warning({
              title: '保存文件路径成功',
              desc: '文件：' + file.name + '保存文件路径成功',
            });
          } else {
            this.$Notice.warning({
              title: '保存文件路径失败',
              desc: '文件：' + file.name + '保存文件路径失败',
            });
          }
        });
      } else {
        // 回调失败也算上传失败，要清除this.$refs.upload.fileList的数据残留，不展示上传失败文件
        const fileList = this.$refs.upload.fileList;
        fileList.splice(fileList.indexOf(file), 1);
        let result = this.parserXml(res);
        this.$Notice.warning({
          title: '文件返回访问地址失败',
          desc: '文件：' + file.name + '<br>' + result.code + '<br>' + result.message,
        });
      }
    },
    handleError(error, file) {
      console.log('handleError');
      let res = this.parserXml(file);
      this.$Notice.warning({
        title: '文件上传失败',
        desc: res.code + '<br>' + res.message,
      });
    },
    /************************************** 转化xml对象，提取code和message节点的内容 ******************************************************/
    parserXml(xmlStr) {
      let xmlDoc = this.xmlParser.parseFromString(xmlStr, 'text/xml');
      let code = xmlDoc.firstChild.childNodes;
      let result = {
        code: code[1].innerHTML,
        message: code[3].innerHTML,
      };
      return result;
    },
    /**************************************获取当前时间 格式：yyyy-MM-dd HH:MM:SS ******************************************************/
    getCurrentTime() {
      let date = new Date(); //当前时间
      let month = this.zeroFill(date.getMonth() + 1); //月
      let day = this.zeroFill(date.getDate()); //日
      let hour = this.zeroFill(date.getHours()); //时
      let minute = this.zeroFill(date.getMinutes()); //分
      let second = this.zeroFill(date.getSeconds()); //秒
      //当前时间
      let curTime = date.getFullYear() + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second;
      return curTime;
    },
    /**************************************  补零 ******************************************************/
    zeroFill(i) {
      if (i <= 9 && i >= 0) {
        return '0' + i;
      } else {
        return i;
      }
    },
    checkUploadList() {
      console.log(this.uploadList);
    },
  },
  mounted() {
    this.uploadList = this.$refs.upload.fileList;
  },
};
</script>
<style scoped>
.upload-list {
  list-style-type: none;
  position: relative;
  margin-right: 4px;
}
.upload-list-a {
  display: none;
  float: right;
  text-decoration: underline;
  font-size: 12px;
  height: 20px;
  line-height: 20px;
}
.upload-list-span {
  display: inline-block;
  float: right;
  margin-right: 15px;
  font-size: 12px;
  height: 20px;
  line-height: 20px;
}
.upload-list:hover .upload-list-a {
  display: block;
}
.upload-list:hover > span {
  color: #2d8cf0;
}
</style>
