import server from '@/common/server';

//根据type查询字典列表
export function questlistDictVOs(typeId) {
  return server.get({
    url: '/base/dict/listDictVOs',
    params: {
      type: typeId,
    },
  });
}
//生成视频上传签名
export function generateUploadSignature() {
  return server.get({
    url: '/base/vedio/generateUploadSignature',
  });
}
//生成超级播放器签名
export function generatePlayerSignature(fileId) {
  return server.get({
    url: '/base/vedio/generatePlayerSignature/' + fileId,
  });
}
