import CryptoJS from 'crypto-js';
//加密
export function encrypt(aesKey, value) {
  let data = '';
  if (typeof value === 'object') {
    data = JSON.stringify(value);
  } else {
    data = value;
  }
  let encrypted = CryptoJS.AES.encrypt(CryptoJS.enc.Utf8.parse(data), CryptoJS.enc.Utf8.parse(aesKey), {
    mode: CryptoJS.mode.ECB,
    padding: CryptoJS.pad.Pkcs7,
  });
  return encrypted.toString();
}
//解密
export function decrypt(aesKey, value) {
  let decrypt = CryptoJS.AES.decrypt(value, CryptoJS.enc.Utf8.parse(aesKey), {
    mode: CryptoJS.mode.ECB,
    padding: CryptoJS.pad.Pkcs7,
  });
  let decString = CryptoJS.enc.Utf8.stringify(decrypt).toString();
  if (checkIsJSON(decString)) {
    return JSON.parse(decString);
  } else {
    return decString;
  }
}

function checkIsJSON(str) {
  if (typeof str === 'string') {
    try {
      let obj = JSON.parse(str);
      return !!(typeof obj === 'object' && obj);
    } catch (e) {
      return false;
    }
  }
  return false;
}
