<template>
  <div>
    <Row>
      <div class="mainDiv">
        <div style="display: flex">
          <div>
            <Input
              search
              v-model.trim="searchData.keyword"
              @on-search="queryList"
              placeholder="按科室名称搜索"
              style="width: 180px"
              clearable
            />
          </div>
        </div>
        <div style="display: flex">
          <Button type="primary" @click="queryList" icon="md-search" style="margin-right: 5px">查询</Button>
          <Button type="primary" @click="showMod('create')" icon="md-add" style="margin-right: 5px">新增</Button>
          <Button type="primary" @click="showMod('update')" icon="md-create" style="margin-right: 5px">修改</Button>
          <!--          <Button type="primary" icon="ios-download-outline" @click="downloadTemplate" style="margin-right: 5px"-->
          <!--            >下载模板</Button-->
          <!--          >-->
          <!--          <Upload action="" :before-upload="importBeforeUpload" accept=".xls, .xlsx" style="float: left; height: 32px">-->
          <!--            <Button type="primary" icon="ios-cloud-upload-outline" style="margin-right: 5px">导入</Button>-->
          <!--          </Upload>-->
          <Button type="primary" icon="ios-log-out" @click="exportData" style="margin-right: 5px">导出</Button>
          <Button type="error" @click="delData" icon="md-trash">删除</Button>
        </div>
      </div>
    </Row>
    <Row>
      <div style="width: 100%">
        <Table
          border
          ref="table"
          row-key="id"
          size="small"
          :height="dataMain.tableHeight"
          :width="dataMain.tableWidth"
          :columns="dataMain.columns"
          highlight-row
          :loading="dataMain.loading"
          @on-row-click="handleRowChange"
          :data="dataMain.data"
        >
        </Table>
      </div>
    </Row>
    <Modal
      draggable1
      mask
      :mask-closable="false"
      v-model="modalData.modaldetail"
      :title="modalData.detailText"
      class="modHeight"
      :footerHide="true"
    >
      <Form ref="currentRow" :model="formData.currentRow" :label-width="110" :rules="formData.formRules">
        <FormItem label="上级科室：" prop="pcode" v-if="!isShow">
          <Select v-model="formData.currentRow.pcode" filterable placeholder="请选择">
            <Option v-for="item in formData.deptList" :value="item.code" :key="item.code">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="科室名称：" prop="name">
          <Input v-model="formData.currentRow.name"></Input>
        </FormItem>
        <FormItem label="科室编码：" prop="code">
          <Input v-model="formData.currentRow.code"></Input>
        </FormItem>
        <FormItem label="科室识别码：" prop="sbm">
          <Input v-model="formData.currentRow.sbm"></Input>
        </FormItem>
      </Form>
      <div class="ivu-modal-footer">
        <slot name="footer">
          <Button @click.native="modalData.modaldetail = false">取 消</Button>
          <Button type="primary" @click="save">确 定</Button>
        </slot>
      </div>
    </Modal>
    <!--   是否上传确认框   -->
    <Modal
      mask
      :mask-closable="false"
      v-model="importData.uploadModal"
      title="是否上传数据？"
      @on-ok="confirmUpload"
      @on-cancel="cancelUpload"
      class="modHeight"
    >
      <p>确定要用 {{ this.importData.importName }}数据 覆盖当前数据吗？</p>
    </Modal>
    <!--   错误信息提示框   -->
    <Modal
      draggable1
      mask
      :mask-closable="false"
      v-model="importData.errorModal"
      :title="importData.importName + '错误信息提示！'"
      class="modHeight"
      :footerHide="true"
    >
      <div style="width: 100%">
        <Table
          border
          ref="errorTable"
          :height="importData.tableHeight"
          :width="importData.tableWidth"
          :columns="importData.importErrorMessageTableColumn"
          highlight-row
          :loading="importData.loading"
          :data="importData.checkData"
        >
        </Table>
      </div>
      <div class="ivu-modal-footer">
        <slot name="footer">
          <Button @click.native="importData.errorModal = false">取 消</Button>
          <Button type="primary" @click.native="importData.errorModal = false">确 定</Button>
        </slot>
      </div>
    </Modal>
    <Modal
      v-model="modalData.deleteModal"
      :title="'是否删除' + dataMain.handleRow.deptName + '？'"
      @on-ok="delOk"
      class="modHeight"
      @on-cancel="delCancel"
    >
      <p>{{ dataMain.handleRow.deptName }}</p>
    </Modal>
  </div>
</template>
<!--标准科室页面-->
<script>
import {
  getDeptBz,
  saveDeptBz,
  updateDeptBz,
  deleteDeptBz,
  getSuperiorDeptBz,
  importDeptBz,
  downloadTemp,
  getRegionSelect,
  importCheck,
} from './standardDepartmentAPI';
export default {
  name: 'standardDepartment',
  data() {
    return {
      isShow: false,
      //模态框数据
      modalData: {
        detailText: '',
        modaldetail: false,
        deleteModal: false,
      },
      //查询数据
      searchData: {
        keyword: '',
      },
      dataMain: {
        columns: [
          {
            title: 'No.',
            width: 120,
            align: 'left',
            tree: true,
            className: 'table_cell_align_left',
            render: (h, params) => {
              return h('span', params.index + 1);
            },
          },
          { title: '科室名称', key: 'deptName', minWidth: 120, align: 'center' },
          { title: '科室编码', key: 'deptCode', minWidth: 140, align: 'center' },
          { title: '识别码', key: 'deptSbm', minWidth: 140, align: 'center' },
          // { title: '所属地区', key: 'regionName', minWidth: 140, align: 'center' },
        ],
        data: [],
        tableWidth: '',
        tableHeight: window.innerHeight - 230,
        loading: false,
        handleRow: [],
      },
      // 表单
      formData: {
        deptList: [],
        regionList: [],
        currentRow: {
          id: 0,
          pcode: 0,
          name: '',
          code: '',
          sbm: '',
          regionId: '',
        },
        formRules: {
          name: [{ required: true, message: '请填写科室名称', trigger: 'blur' }],
          code: [{ required: true, message: '请填写科室编码', trigger: 'blur' }],
          sbm: [{ required: true, message: '请填写识别码', trigger: 'blur' }],
          // regionId: [{ required: true, message: '请选择地区', trigger: 'blur' }],
        },
      },
      // 导入
      importData: {
        formData: new FormData(), // 将文件转化成formdata类型
        uploadModal: false, // 是否展示提示框
        importName: '', // 提示内容
        checkData: [], // 错误信息
        errorModal: false, // 错误信息提示

        tableWidth: '',
        tableHeight: window.innerHeight - 420,
        loading: false,

        importErrorMessageTableColumn: [
          { title: '检查项', key: 'checkItem', minWidth: 120, align: 'center' },
          { title: '错误位置', key: 'errorLocation', minWidth: 140, align: 'center' },
          { title: '错误信息', key: 'errorMessage', minWidth: 140, align: 'center' },
        ],
      },
    };
  },
  methods: {
    queryList() {
      this.dataMain.loading = true;
      getDeptBz(this.searchData.keyword).then(res => {
        if (res.data) {
          this.dataMain.data = res.data;
          this.dataMain.loading = false;
        }
      });
    },
    queryDeptList() {
      getSuperiorDeptBz().then(res => {
        if (res.data) {
          this.formData.deptList = res.data;
        }
      });
    },
    queryRegionList() {
      getRegionSelect().then(res => {
        if (res.data) {
          this.formData.regionList = res.data;
        }
      });
    },
    showMod(val) {
      this.$refs['currentRow'].resetFields();

      this.queryDeptList();
      this.queryRegionList();
      if (val === 'create') {
        this.modalData.detailText = '新增标准科室';
        this.modalData.modaldetail = true;
        this.isShow = false;
      } else if (val === 'update') {
        if (this.dataMain.handleRow.length === 0) {
          return this.$Message.error('请选择要修改的数据');
        }

        this.modalData.detailText = '修改标准科室';
        this.formData.currentRow = {
          id: this.dataMain.handleRow.fid,
          code: this.dataMain.handleRow.deptCode,
          pcode: this.dataMain.handleRow.pid,
          name: this.dataMain.handleRow.deptName,
          sbm: this.dataMain.handleRow.deptSbm,
          // regionId: this.dataMain.handleRow.regionId,
        };
        if (this.formData.currentRow.pcode === '0') {
          this.isShow = true;
        }
        this.modalData.modaldetail = true;
      }
    },
    //删除方法
    delData() {
      if (this.dataMain.handleRow.length === 0) {
        return this.$Message.error('请选择要删除的数据');
      }
      this.modalData.deleteModal = true;
    },
    delOk() {
      if (this.dataMain.handleRow.length === 0) {
        return this.$Message.error('请选择要删除的数据');
      }
      deleteDeptBz(this.dataMain.handleRow.fid).then(res => {
        // setTimeout(msg, 0);
        if (res.code === 0) {
          this.$Message.success('删除成功');
          this.dataMain.handleRow = [];
          this.queryList();
        } else {
          this.$Message.error('删除失败');
        }
      });
    },
    delCancel() {
      this.modalData.deleteModal = false;
    },
    save() {
      this.$refs['currentRow'].validate(valid => {
        if (valid) {
          if (this.modalData.detailText === '新增标准科室') {
            saveDeptBz(this.formData.currentRow)
              .then(res => {
                if (res.code === 0) {
                  this.$Message.success('新增成功');
                  this.modalData.modaldetail = false;
                  this.queryList();
                } else {
                  this.$Message.error(res.msg);
                }
              })
              .catch(res => {
                console.log('error:' + res);
              });
          } else {
            updateDeptBz(this.formData.currentRow)
              .then(res => {
                if (res.code === 0) {
                  this.$Message.success('修改成功');
                  this.modalData.modaldetail = false;
                  this.dataMain.handleRow = [];
                  this.queryList();
                } else {
                  this.$Message.error(res.msg);
                }
              })
              .catch(res => {
                console.log('error:' + res);
              });
          }
        }
      });
    },
    handleRowChange(currentRow) {
      this.dataMain.handleRow = currentRow;
    },
    // 导出数据
    exportData() {
      downloadTemp('导出');
    },
    // 导出模板
    downloadTemplate() {
      downloadTemp('下载模板');
    },
    //导入前判断文件类型
    importBeforeUpload(file) {
      // 获取文件后缀名
      const fileExt = file.name.split('.').pop().toLocaleLowerCase();
      if (fileExt === 'xlsx' || fileExt === 'xls') {
        this.importData.formData = new FormData();
        this.importData.formData.append('file', file);
        this.importData.importName = this.importData.formData.get('file').name;
        importCheck(this.importData.formData).then(res => {
          if (res.data.length === 0) {
            this.importData.uploadModal = true;
          } else {
            this.importData.checkData = res.data;
            this.importData.errorModal = true;
          }
        });
      } else {
        this.$Notice.warning({
          title: '文件类型错误',
          desc: '文件：' + file.name + '不是EXCEL文件，请选择后缀为.xlsx或者.xls的EXCEL文件。',
        });
      }
      return false;
    },
    //
    confirmUpload() {
      const msg = this.$Message.loading({
        content: '正在导入，请稍候...',
        duration: 0,
      });
      importDeptBz(this.importData.formData).then(res => {
        setTimeout(msg, 0);
        if (res.data.code === 0) {
          this.queryList();
          this.importData.uploadModal = false;
          this.$Message.success(res.data.msg);
        }
      });
    },
    cancelUpload() {
      //清空formdata
      this.importData.formData = null;
      this.importData.formData = new FormData();
      this.importData.uploadModal = false;
    },
  },
  mounted() {
    this.dataMain.tableHeight = window.innerHeight - 230;
    this.queryList();
    this.queryDeptList();
  },
};
</script>

<style scoped>
.mainDiv {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 10px;
}
</style>
