import server from '../../../common/server';

//查询主页面
export function getDeptBz(keyword, startDate, endDate, pageData) {
  return server.get({
    url: 'platform/financialworkload/getRepairFinancialworkloadData',
    params: {
      keyword: keyword,
      startDate: startDate,
      endDate: endDate,
      'pageData.limit': pageData.pageSize,
      'pageData.order': pageData.order,
      'pageData.orderField': '',
      'pageData.page': pageData.pageNumber,
    },
  });
}
//新增数据
export function saveDeptBz(data) {
  return server.post({
    url: 'platform/financialworkload/saveWorkloadData',
    params: data,
  });
}
//同步工作量数据
export function syncFinancialWorkloadtType() {
  return server.post({
    url: 'platform/financialworkloadtype/syncFinancialWorkloadtType',
  });
}
//修改数据
export function updateDeptBz(data) {
  return server.put({
    url: 'base/deptbz/updateDeptBz',
    params: {
      id: data.id,
      code: data.code,
      pcode: data.pcode,
      name: data.name,
      sbm: data.sbm,
      regionId: data.regionId,
    },
  });
}
//根据id删除
export function deleteDeptBz(id) {
  return server.del({
    url: 'platform/financialworkload/deleteWorkloadByid/' + id,
  });
}
//查询上级科室
export function getSuperiorDeptBz() {
  return server.get({
    url: 'base/deptbz/getDeptBz',
    params: {
      deptName: '',
    },
  });
}
//查询上级科室1
export function getSuperiorDept() {
  return server.post({
    url: 'platform/financialworkloadtype/getKdDept',
  });
}
export function getFinancialWorkloadtTypeDict() {
  return server.post({
    url: 'platform/financialworkloadtype/getFinancialWorkloadtTypeDict',
  });
}
// 导入数据
export function importDeptBz(file) {
  return server.uploadFile({
    url: 'base/deptbz/importDeptBz',
    params: file,
  });
}
export function downloadTemp(type) {
  let url = '';
  if (type === '导出') {
    url = '/base/deptbz/exportDeptBz';
  } else {
    url = '/base/deptbz/exportDeptBzTemplate';
  }
  return server.downloadFile({
    url: url,
  });
}
//查询上地区列表
export function getRegionSelect() {
  return server.get({
    url: 'base/deptbz/getRegionSelect',
  });
}
// 导入数据验证
export function importCheck(file) {
  return server.uploadFile({
    url: 'base/deptbz/importCheck',
    params: file,
  });
}
