<template>
  <div>
    <Row>
      <div class="mainDiv">
        <div style="display: flex">
          <div>
            <DatePicker
              type="daterange"
              :value="searchData.mainData"
              placeholder="请选择日期范围"
              style="width: 200px; margin-right: 10px"
              @on-change="changeMainData"
              :options="dateRule"
              placement="bottom-start"
            />
            <Input
              search
              v-model.trim="searchData.keyword"
              @on-search="queryList"
              placeholder="按收入项目名称搜索"
              style="width: 180px"
              clearable
            />
          </div>
        </div>
        <div style="display: flex">
          <Button type="primary" @click="queryList" icon="md-search" style="margin-right: 5px">查询</Button>
          <Button type="primary" @click="showMod('create')" icon="md-add" style="margin-right: 5px">新增</Button>
          <!--          <Button type="primary" icon="ios-log-out" @click="exportData" style="margin-right: 5px">导出</Button>-->
          <Button type="error" @click="delData" icon="md-trash">删除</Button>
        </div>
      </div>
    </Row>
    <Row>
      <div style="width: 100%">
        <Table
          border
          ref="table"
          row-key="id"
          size="small"
          :height="dataMain.tableHeight"
          :width="dataMain.tableWidth"
          :columns="dataMain.columns"
          highlight-row
          :loading="dataMain.loading"
          @on-row-click="handleRowChange"
          :data="dataMain.data"
        />
        <Page
          ref="page"
          style="margin-top: 10px"
          align="right"
          :total="dataMain.total"
          :current="dataMain.pageNumber"
          :page-size="dataMain.pageSize"
          :page-size-opts="[10, 20, 50, 100]"
          show-elevator
          show-total
          show-sizer
          @on-change="page_change_table"
          @on-page-size-change="pageSizeChange"
        />
      </div>
    </Row>
    <Modal
      draggable1
      mask
      :mask-closable="false"
      v-model="modalData.modaldetail"
      :title="modalData.detailText"
      class="modHeight"
      width="700"
      :footerHide="true"
    >
      <Form ref="currentRow" :model="formData.currentRow" :label-width="110" :rules="formData.formRules">
        <Row>
          <Col span="12">
            <FormItem label="发生时间：" prop="fdate">
              <DatePicker
                v-model="formData.currentRow.fdate"
                type="date"
                placeholder="请选择发生时间"
                style="width: 200px"
                :options="dateRule2"
              ></DatePicker>
            </FormItem>
            <FormItem label="门诊/住院：" prop="fincometype">
              <RadioGroup v-model="formData.currentRow.fincometype">
                <Radio label="门诊"></Radio>
                <Radio label="住院"></Radio>
              </RadioGroup>
            </FormItem>
            <FormItem label="开单科室：" prop="fhrpkdksname">
              <Select
                v-model="formData.currentRow.fhrpkdksname"
                filterable
                placeholder="请选择开单科室"
                @on-change="getKdDeptMsg"
              >
                <Option v-for="item in formData.deptList" :value="item.name + ',' + item.id" :key="item.id">{{
                  item.name
                }}</Option>
              </Select>
            </FormItem>
            <!--            <FormItem label="患者所在科室：">-->
            <!--              <Select-->
            <!--                v-model="formData.currentRow.fhrpbrszksname"-->
            <!--                filterable-->
            <!--                placeholder="病人所在科室"-->
            <!--                @on-change="getBrDeptMsg"-->
            <!--              >-->
            <!--                <Option-->
            <!--                  v-for="item in formData.deptList"-->
            <!--                  :value="item.deptName + ',' + item.fid"-->
            <!--                  :key="item.deptName"-->
            <!--                  >{{ item.deptName }}</Option-->
            <!--                >-->
            <!--              </Select>-->
            <!--            </FormItem>-->
          </Col>
          <Col span="12">
            <FormItem label="收入项目：" prop="fhrpincomename">
              <!--              <Select-->
              <!--                v-model="formData.currentRow.fhrpincomename"-->
              <!--                filterable-->
              <!--                placeholder="请选择收入项目"-->
              <!--                @on-change="getIncomename"-->
              <!--              >-->
              <!--                <Option v-for="item in formData.IncomeTypeList" :value="item.name + ',' + item.id" :key="item.name">{{-->
              <!--                  item.name-->
              <!--                }}</Option>-->
              <!--     @on-change="getIncomename"         </Select>-->
              <Cascader
                :data="formData.IncomeTypeList"
                change-on-select
                :render-format="format"
                v-model="formData.currentRow.fhrpincomeData"
                @on-change="getIncomename"
                trigger="hover"
                placeholder="选择收入项目"
              />
            </FormItem>
            <FormItem label="金额：" prop="fmoney">
              <InputNumber style="width: 100%" v-model="formData.currentRow.fmoney" placeholder="请输入金额" min="0" />
            </FormItem>
            <FormItem label="执行科室：" prop="fhrpzxksname">
              <Select
                v-model="formData.currentRow.fhrpzxksname"
                filterable
                placeholder="请选择执行科室"
                @on-change="getZxDeptMsg"
              >
                <Option v-for="item in formData.deptList" :value="item.name + ',' + item.id" :key="item.id">{{
                  item.name
                }}</Option>
              </Select>
            </FormItem>
          </Col>
        </Row>
        <FormItem label="说明：">
          <Input v-model="formData.currentRow.fbz" type="textarea"></Input>
        </FormItem>
      </Form>
      <div class="ivu-modal-footer">
        <slot name="footer">
          <Button @click.native="modalData.modaldetail = false">取 消</Button>
          <Button type="primary" @click="save">确 定</Button>
        </slot>
      </div>
    </Modal>
    <!--   是否上传确认框   -->
    <Modal
      mask
      :mask-closable="false"
      v-model="importData.uploadModal"
      title="是否上传数据？"
      @on-ok="confirmUpload"
      @on-cancel="cancelUpload"
      class="modHeight"
    >
      <p>确定要用 {{ this.importData.importName }}数据 覆盖当前数据吗？</p>
    </Modal>
    <!--   错误信息提示框   -->
    <Modal
      draggable1
      mask
      :mask-closable="false"
      v-model="importData.errorModal"
      :title="importData.importName + '错误信息提示！'"
      class="modHeight"
      :footerHide="true"
    >
      <div style="width: 100%">
        <Table
          border
          ref="errorTable"
          :height="importData.tableHeight"
          :width="importData.tableWidth"
          :columns="importData.importErrorMessageTableColumn"
          highlight-row
          :loading="importData.loading"
          :data="importData.checkData"
        >
        </Table>
      </div>
      <div class="ivu-modal-footer">
        <slot name="footer">
          <Button @click.native="importData.errorModal = false">取 消</Button>
          <Button type="primary" @click.native="importData.errorModal = false">确 定</Button>
        </slot>
      </div>
    </Modal>
    <Modal
      v-model="modalData.deleteModal"
      :title="'是否删除' + dataMain.handleRow.fhrpincomename + '？'"
      @on-ok="delOk"
      class="modHeight"
      @on-cancel="delCancel"
    >
      <p>确认删除？</p>
    </Modal>
  </div>
</template>
<!--收入管理页面-->
<script>
import {
  getDeptBz,
  saveDeptBz,
  updateDeptBz,
  deleteDeptBz,
  getSuperiorDeptBz,
  getHisIncomeType,
  importDeptBz,
  downloadTemp,
  importCheck,
} from './incomeManage';
import dayjs from 'dayjs';
export default {
  name: 'workloadManage',
  data() {
    return {
      isShow: false,
      //模态框数据
      modalData: {
        detailText: '',
        modaldetail: false,
        deleteModal: false,
      },
      //查询数据
      searchData: {
        mainData: [],
        keyword: '',
      },
      queryData: {
        beginDate: '',
        endDate: '',
      },
      dataMain: {
        columns: [
          {
            title: 'No.',
            width: 60,
            align: 'left',
            render: (h, params) => {
              return h('span', params.index + 1);
            },
          },
          { title: '开单科室名称', key: 'fhrpkdksname', minWidth: 120, align: 'center' },
          { title: '收入项目类型', key: 'fincometype', minWidth: 120, align: 'center' },
          { title: '收入项目名称', key: 'fhrpincomename', minWidth: 120, align: 'center' },
          { title: '金额', key: 'fmoney', minWidth: 80, align: 'center' },
          { title: '说明', key: 'fbz', minWidth: 120, align: 'center', tooltip: true },
          {
            title: '发生时间',
            minWidth: 120,
            align: 'center',
            render: (h, params) => {
              return h('span', params.row.fdate.split(' ')[0]);
            },
          },
          { title: '添加时间', key: 'faddtime', minWidth: 120, align: 'center' },
        ],
        data: [],
        tableWidth: '',
        tableHeight: 0,
        loading: false,
        handleRow: [],
        total: 0, //总数
        index: -1, //当前行的index
        pageNumber: 1, //初始化加载第一页，默认第一页
        pageSize: 10, //初始每页数量
      },
      // 表单
      formData: {
        deptList: [],
        regionList: [],
        IncomeTypeList: [],
        currentRow: {
          id: 0,
          fhrpincomeData: [],
          fdate: this.defaultDate(-1),
          fincometype: '门诊',
          fhrpkdksname: '',
          fhrpkdksid: '',
          fhrpbrszksname: '',
          fhrpbrszksid: '',
          fmoney: '',
          fhrpzxksname: '',
          fhrpzxksid: '',
          fhrpincomename: '',
          fhrpincomeid: '',
          fintype: 0,
          ffb: '',
          fbz: '',
          faddtime: this.defaultDate(-1),
          fhid: this.s_orgID,
        },
        formRules: {
          fhrpkdksname: [{ required: true, message: '请选择开单科室', trigger: 'blur,change' }],
          fhrpzxksname: [{ required: true, message: '请选择执行科室', trigger: 'blur,change' }],
          code: [{ required: true, message: '请填写科室编码', trigger: 'blur' }],
          fhrpincomename: [{ required: true, message: '请选择收入项目', trigger: 'blur,change' }],
          fmoney: [{ required: true, message: '请输入金额' }],
        },
      },
      // 导入
      importData: {
        formData: new FormData(), // 将文件转化成formdata类型
        uploadModal: false, // 是否展示提示框
        importName: '', // 提示内容
        checkData: [], // 错误信息
        errorModal: false, // 错误信息提示

        tableWidth: '',
        tableHeight: window.innerHeight - 420,
        loading: false,

        importErrorMessageTableColumn: [
          { title: '检查项', key: 'checkItem', minWidth: 120, align: 'center' },
          { title: '错误位置', key: 'errorLocation', minWidth: 140, align: 'center' },
          { title: '错误信息', key: 'errorMessage', minWidth: 140, align: 'center' },
        ],
      },
      dateRule: {
        disabledDate(date) {
          if (dayjs().isBefore(dayjs().format('YYYY-MM-DD 12:00:00'))) {
            return date && date.valueOf() > Date.now() - 1000 * 60 * 60 * 24 * 1;
          } else {
            return date && date.valueOf() > Date.now() - 1000 * 60 * 60 * 24 * 1;
          }
        },
      },
      dateRule2: {
        disabledDate (date) {
          return date && date.valueOf() > Date.now() - 86400000;
        }
      },
    };
  },
  methods: {
    page_change_table(val) {
      //页码改变
      this.dataMain.pageNumber = val;
      this.queryDeptList();
    },
    pageSizeChange(val) {
      // 页码size改变
      this.dataMain.pageSize = val;
      this.queryDeptList();
    },
    //选中主页面日期
    changeMainData(date) {
      this.queryData.beginDate = date[0];
      this.queryData.endDate = date[1];
    },
    queryListFromButton() {
      this.dataMain.pageNumber = 1;
      this.queryList();
    },
    queryList() {
      this.dataMain.loading = true;
      let page = {
        total: this.dataMain.total,
        pageNumber: this.dataMain.pageNumber,
        pageSize: this.dataMain.pageSize,
        order: 'id',
      };
      getDeptBz(this.searchData.keyword, this.queryData.beginDate, this.queryData.endDate, page).then(res => {
        if (res.data) {
          this.dataMain.data = res.data.list;
          this.dataMain.total = res.data.total;
          this.dataMain.loading = false;
        }
      });
    },
    queryDeptList() {
      getSuperiorDeptBz().then(res => {
        if (res.data) {
          this.formData.deptList = res.data;
        }
      });
    },
    getHisIncomeType() {
      getHisIncomeType().then(res => {
        if (res.data) {
          this.formData.IncomeTypeList = res.data;
        }
      });
    },
    showMod(val) {
      this.$refs['currentRow'].resetFields();
      this.queryDeptList();
      this.getHisIncomeType();
      if (val === 'create') {
        this.formData.currentRow.fbz = '';
        this.modalData.detailText = '新增收入数据';
        this.modalData.modaldetail = true;
        this.formData.currentRow.fhrpincomeData = [];
        this.isShow = false;
      } else if (val === 'update') {
        if (this.dataMain.handleRow.length === 0) {
          return this.$Message.error('请选择要修改的数据');
        }
        this.modalData.detailText = '修改收入数据';
        this.formData.currentRow = {
          id: this.dataMain.handleRow.bzid,
          code: this.dataMain.handleRow.deptCode,
          pcode: this.dataMain.handleRow.deptpcode,
          name: this.dataMain.handleRow.deptName,
          sbm: this.dataMain.handleRow.deptSbm,
          // regionId: this.dataMain.handleRow.regionId,
        };
        if (this.formData.currentRow.pcode === '0') {
          this.isShow = true;
        }
        this.modalData.modaldetail = true;
      }
    },
    //删除方法
    delData() {
      if (this.dataMain.handleRow.length === 0) {
        return this.$Message.error('请选择要删除的数据');
      }
      this.modalData.deleteModal = true;
    },
    delOk() {
      if (this.dataMain.handleRow.length === 0) {
        return this.$Message.error('请选择要删除的数据');
      }
      deleteDeptBz(this.dataMain.handleRow.fid).then(res => {
        // setTimeout(msg, 0);
        if (res.code === 0) {
          this.$Message.success('删除成功');
          this.dataMain.handleRow = [];
          this.queryList();
        } else {
          this.$Message.error('删除失败');
        }
      });
    },
    delCancel() {
      this.modalData.deleteModal = false;
    },
    save() {
      this.$refs['currentRow'].validate(valid => {
        if (valid) {
          let obj = {
            fdate: dayjs(this.formData.currentRow.fdate).format('YYYY-MM-DD'),
            fincometype: this.formData.currentRow.fincometype,
            fhrpkdksname: this.formData.currentRow.fhrpkdksname.split(',')[0],
            fhrpkdksid: this.formData.currentRow.fhrpkdksid,
            // fhrpbrszksname: this.formData.currentRow.fhrpbrszksname.split(',')[0],
            fhrpbrszksid: this.formData.currentRow.fhrpbrszksid,
            fmoney: parseFloat(this.formData.currentRow.fmoney),
            fhrpzxksname: this.formData.currentRow.fhrpzxksname.split(',')[0],
            fhrpzxksid: this.formData.currentRow.fhrpzxksid,
            fhrpincomename: this.formData.currentRow.fhrpincomename,
            fhrpincomeid: this.formData.currentRow.fhrpincomeid,
            fintype: this.formData.currentRow.fintype,
            ffb: this.formData.currentRow.ffb,
            fbz: this.formData.currentRow.fbz,
            faddtime: this.defaultDate(-1),
            fhid: this.s_orgID,
          };
          if (this.modalData.detailText === '新增收入数据') {
            saveDeptBz(obj)
              .then(res => {
                if (res.code === 0) {
                  this.$Message.success('新增成功');
                  this.modalData.modaldetail = false;
                  this.queryList();
                } else {
                  this.$Message.error(res.msg);
                }
              })
              .catch(res => {
                console.log('error:' + res);
              });
          } else {
            updateDeptBz(this.formData.currentRow)
              .then(res => {
                if (res.code === 0) {
                  this.$Message.success('修改成功');
                  this.modalData.modaldetail = false;
                  this.dataMain.handleRow = [];
                  this.queryList();
                } else {
                  this.$Message.error(res.msg);
                }
              })
              .catch(res => {
                console.log('error:' + res);
              });
          }
        }
      });
    },
    getKdDeptMsg(obj) {
      let list = obj.split(',');
      this.formData.currentRow.fhrpkdksid = list[1];
    },
    getBrDeptMsg(obj) {
      let list = obj.split(',');
      this.formData.currentRow.fhrpbrszksid = list[1];
    },
    getZxDeptMsg(obj) {
      let list = obj.split(',');
      this.formData.currentRow.fhrpzxksid = list[1];
    },
    getIncomename(value, selectedData) {
      if (selectedData.length > 0) {
        const index = value.length - 1;
        this.formData.currentRow.fhrpincomename = selectedData[index].name;
        this.formData.currentRow.fhrpincomeid = selectedData[index].id;
      } else {
        this.formData.currentRow.fhrpincomename = '';
        this.formData.currentRow.fhrpincomeid = '';
      }
    },
    handleRowChange(currentRow) {
      this.dataMain.handleRow = currentRow;
    },
    format(labels) {
      const index = labels.length - 1;
      return labels[index];
    },
    // 导出数据
    exportData() {
      downloadTemp('导出');
    },
    // 导出模板
    downloadTemplate() {
      downloadTemp('下载模板');
    },
    //导入前判断文件类型
    importBeforeUpload(file) {
      // 获取文件后缀名
      const fileExt = file.name.split('.').pop().toLocaleLowerCase();
      if (fileExt === 'xlsx' || fileExt === 'xls') {
        this.importData.formData = new FormData();
        this.importData.formData.append('file', file);
        this.importData.importName = this.importData.formData.get('file').name;
        importCheck(this.importData.formData).then(res => {
          if (res.data.length === 0) {
            this.importData.uploadModal = true;
          } else {
            this.importData.checkData = res.data;
            this.importData.errorModal = true;
          }
        });
      } else {
        this.$Notice.warning({
          title: '文件类型错误',
          desc: '文件：' + file.name + '不是EXCEL文件，请选择后缀为.xlsx或者.xls的EXCEL文件。',
        });
      }
      return false;
    },
    confirmUpload() {
      const msg = this.$Message.loading({
        content: '正在导入，请稍候...',
        duration: 0,
      });
      importDeptBz(this.importData.formData).then(res => {
        setTimeout(msg, 0);
        if (res.data.code === 0) {
          this.queryList();
          this.importData.uploadModal = false;
          this.$Message.success(res.data.msg);
        }
      });
    },
    cancelUpload() {
      //清空formdata
      this.importData.formData = null;
      this.importData.formData = new FormData();
      this.importData.uploadModal = false;
    },
    defaultDate(time) {
      // let myDate = new Date(y, m - 1, 0); //获取上个月最后一天
      let date = new Date();
      date.setDate(date.getDate() + time);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      let d = date.getDate();
      m = m < 10 ? '0' + m : m;
      d = d < 10 ? '0' + d : d;
      let time2 = y + '-' + m + '-' + d;
      return time2;
    },
  },
  mounted() {
    this.searchData.mainData = [this.defaultDate(-30), this.defaultDate(-1)];
    this.queryData.beginDate = this.searchData.mainData[0];
    this.queryData.endDate = this.searchData.mainData[1];
    this.dataMain.tableHeight = window.innerHeight - this.$refs.table.$el.offsetTop - 95;
    window.onresize = () => {
      return (() => {
        this.dataMain.tableHeight = window.innerHeight - this.$refs.table.$el.offsetTop - 95;
      })();
    };
    this.queryList();
    this.queryDeptList();
    this.getHisIncomeType();
  },
};
</script>

<style scoped>
.mainDiv {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 10px;
}
</style>
