<template>
  <div>
    <Row>
      <div class="mainDiv">
        <div style="display: flex">
          <div>
            <DatePicker
              type="daterange"
              :value="searchData.mainData"
              placeholder="请选择日期范围"
              style="width: 200px; margin-right: 10px"
              @on-change="changeMainData"
              :options="dateRule"
              placement="bottom-start"
            />
            <Input
              search
              v-model.trim="searchData.keyword"
              @on-search="queryList"
              placeholder="按工作量项目搜索"
              style="width: 180px"
              clearable
            />
          </div>
        </div>
        <div style="display: flex">
          <Button type="primary" @click="queryList" icon="md-search" style="margin-right: 5px">查询</Button>
          <Button type="primary" @click="showMod('create')" icon="md-add" style="margin-right: 5px">新增</Button>
          <Button
            type="primary"
            @click="synchronization"
            icon="md-refresh"
            style="margin-right: 5px"
            :loading="modalData.synchronizationLoading"
            >更新工作量项目</Button
          >
          <!--          <Button type="primary" icon="ios-log-out" @click="exportData" style="margin-right: 5px">导出</Button>-->
          <Button type="error" @click="delData" icon="md-trash">删除</Button>
        </div>
      </div>
    </Row>
    <Row>
      <div style="width: 100%">
        <Table
          border
          ref="table"
          row-key="id"
          size="small"
          :height="dataMain.tableHeight"
          :width="dataMain.tableWidth"
          :columns="dataMain.columns"
          highlight-row
          :loading="dataMain.loading"
          @on-row-click="handleRowChange"
          :data="dataMain.data"
        />
        <Page
          ref="page"
          style="margin-top: 10px"
          align="right"
          :total="dataMain.total"
          :current="dataMain.pageNumber"
          :page-size="dataMain.pageSize"
          :page-size-opts="[10, 20, 50, 100]"
          show-elevator
          show-total
          show-sizer
          @on-change="page_change_table"
          @on-page-size-change="pageSizeChange"
        />
      </div>
    </Row>
    <Modal
      draggable1
      mask
      :mask-closable="false"
      v-model="modalData.modaldetail"
      :title="modalData.detailText"
      class="modHeight"
      :footerHide="true"
    >
      <Form ref="currentRow" :model="formData.currentRow" :label-width="110" :rules="formData.formRules">
        <FormItem label="发生时间：" prop="fdate">
          <DatePicker
            v-model="formData.currentRow.fdate"
            type="date"
            placeholder="请选择发生时间"
            style="width: 200px"
            :options="dateRule2"
          ></DatePicker>
        </FormItem>
        <FormItem label="科室名称：" prop="fdepartmentname">
          <Select
            v-model="formData.currentRow.fdepartmentname"
            filterable
            placeholder="请选择科室"
            @on-change="getDeptMsg"
          >
            <Option v-for="item in formData.deptList" :value="item.name + ',' + item.id" :key="item.id">{{
              item.name
            }}</Option>
          </Select>
        </FormItem>
        <FormItem label="工作量项目：" prop="fworkloadname">
          <Select
            v-model="formData.currentRow.fworkloadname"
            filterable
            placeholder="请选择工作量项目"
            @on-change="getWorkload"
          >
            <Option v-for="item in formData.WorkloadtTypeDict" :value="item.name + ',' + item.id" :key="item.id">{{
              item.name
            }}</Option>
          </Select>
        </FormItem>
        <FormItem label="次数：" prop="fnumber">
          <InputNumber style="width: 100%" v-model="formData.currentRow.fnumber" placeholder="请输入工作量" min="0" />
        </FormItem>
        <FormItem label="说明：">
          <Input v-model="formData.currentRow.fbz" type="textarea"></Input>
        </FormItem>
      </Form>
      <div class="ivu-modal-footer">
        <slot name="footer">
          <Button @click.native="modalData.modaldetail = false">取 消</Button>
          <Button type="primary" @click="save">确 定</Button>
        </slot>
      </div>
    </Modal>
    <!--   是否上传确认框   -->
    <Modal
      mask
      :mask-closable="false"
      v-model="importData.uploadModal"
      title="是否上传数据？"
      @on-ok="confirmUpload"
      @on-cancel="cancelUpload"
      class="modHeight"
    >
      <p>确定要用 {{ this.importData.importName }}数据 覆盖当前数据吗？</p>
    </Modal>
    <!--   错误信息提示框   -->
    <Modal
      draggable1
      mask
      :mask-closable="false"
      v-model="importData.errorModal"
      :title="importData.importName + '错误信息提示！'"
      class="modHeight"
      :footerHide="true"
    >
      <div style="width: 100%">
        <Table
          border
          ref="errorTable"
          :height="importData.tableHeight"
          :width="importData.tableWidth"
          :columns="importData.importErrorMessageTableColumn"
          highlight-row
          :loading="importData.loading"
          :data="importData.checkData"
        >
        </Table>
      </div>
      <div class="ivu-modal-footer">
        <slot name="footer">
          <Button @click.native="importData.errorModal = false">取 消</Button>
          <Button type="primary" @click.native="importData.errorModal = false">确 定</Button>
        </slot>
      </div>
    </Modal>
    <Modal
      v-model="modalData.deleteModal"
      :title="'是否删除' + dataMain.handleRow.fworkloadname + '？'"
      @on-ok="delOk"
      class="modHeight"
      @on-cancel="delCancel"
    >
      <p>确认删除？</p>
    </Modal>
  </div>
</template>
<!--工作量管理页面-->
<script>
import {
  getDeptBz,
  saveDeptBz,
  updateDeptBz,
  deleteDeptBz,
  getSuperiorDept,
  getFinancialWorkloadtTypeDict,
  importDeptBz,
  downloadTemp,
  getRegionSelect,
  syncFinancialWorkloadtType,
  importCheck,
} from './workloadManage';
import dayjs from 'dayjs';
export default {
  name: 'workloadManage',
  data() {
    const validateFdate = (rule, value, callback) => {
      if (value.length === 0) {
        callback(new Error('请选择发生时间'));
      } else {
        callback();
      }
    };
    return {
      isShow: false,
      //模态框数据
      modalData: {
        detailText: '',
        modaldetail: false,
        deleteModal: false,
        synchronizationLoading: false,
      },
      //查询数据
      searchData: {
        mainData: [],
        keyword: '',
      },
      queryData: {
        beginDate: '',
        endDate: '',
      },
      dataMain: {
        columns: [
          {
            title: 'No.',
            width: 60,
            align: 'left',
            render: (h, params) => {
              return h('span', params.index + 1);
            },
          },
          { title: '科室名称', key: 'fdepartmentname', minWidth: 120, align: 'center' },
          { title: '工作量项目', key: 'fworkloadname', minWidth: 120, align: 'center' },
          { title: '次数', key: 'fnumber', minWidth: 120, align: 'center' },
          { title: '说明', key: 'fbz', minWidth: 120, align: 'center', tooltip: true },
          {
            title: '发生时间',
            minWidth: 120,
            align: 'center',
            render: (h, params) => {
              return h('span', params.row.fdate.split(' ')[0]);
            },
          },
          { title: '添加时间', key: 'faddtime', minWidth: 120, align: 'center' },
        ],
        data: [],
        tableWidth: '',
        tableHeight: 0,
        loading: false,
        handleRow: [],
        total: 0, //总数
        index: -1, //当前行的index
        pageNumber: 1, //初始化加载第一页，默认第一页
        pageSize: 10, //初始每页数量
      },
      // 表单
      formData: {
        deptList: [],
        WorkloadtTypeDict: [],
        regionList: [],
        currentRow: {
          id: 0,
          fworkloadid: '',
          fworkloadname: '',
          fdepartmentid: '',
          fdepartmentname: '',
          fnumber: '',
          fdate: this.defaultDate(-1),
          faddtime: '',
          finName: '客户端录入',
          fintype: 0,
          fhid: this.s_orgID,
          ffb: '',
          fbz: '',
          ffflx: '',
        },
        formRules: {
          fdate: [{ required: true, validator: validateFdate, trigger: 'blur,change' }],
          fdepartmentname: [{ required: true, message: '请选择科室', trigger: 'blur,change' }],
          fworkloadname: [{ required: true, message: '请选择工作量项目', trigger: 'blur,change' }],
          // fnumber: [{ required: true, validator: validateAmount, trigger: 'blur' }],
          fnumber: [
            { required: true, message: '工作量不能为空' },
            // { type: 'number', message: '工作量必须为数字值' },
          ],
        },
      },
      // 导入
      importData: {
        formData: new FormData(), // 将文件转化成formdata类型
        uploadModal: false, // 是否展示提示框
        importName: '', // 提示内容
        checkData: [], // 错误信息
        errorModal: false, // 错误信息提示

        tableWidth: '',
        tableHeight: window.innerHeight - 420,
        loading: false,

        importErrorMessageTableColumn: [
          { title: '检查项', key: 'checkItem', minWidth: 120, align: 'center' },
          { title: '错误位置', key: 'errorLocation', minWidth: 140, align: 'center' },
          { title: '错误信息', key: 'errorMessage', minWidth: 140, align: 'center' },
        ],
      },
      dateRule: {
        disabledDate(date) {
          if (dayjs().isBefore(dayjs().format('YYYY-MM-DD 12:00:00'))) {
            return date && date.valueOf() > Date.now() - 1000 * 60 * 60 * 24 * 1;
          } else {
            return date && date.valueOf() > Date.now() - 1000 * 60 * 60 * 24 * 1;
          }
        },
      },
      dateRule2: {
        disabledDate (date) {
          return date && date.valueOf() > Date.now() - 86400000;
        }
      },
    };
  },
  methods: {
    page_change_table(val) {
      //页码改变
      this.dataMain.pageNumber = val;
      this.queryDeptList();
    },
    pageSizeChange(val) {
      // 页码size改变
      this.dataMain.pageSize = val;
      this.queryDeptList();
    },
    //选中主页面日期
    changeMainData(date) {
      this.queryData.beginDate = date[0];
      this.queryData.endDate = date[1];
    },
    queryListFromButton() {
      this.dataMain.pageNumber = 1;
      this.queryList();
    },
    queryList() {
      this.dataMain.loading = true;
      let page = {
        total: this.dataMain.total,
        pageNumber: this.dataMain.pageNumber,
        pageSize: this.dataMain.pageSize,
        order: 'id',
      };
      getDeptBz(this.searchData.keyword, this.queryData.beginDate, this.queryData.endDate, page).then(res => {
        if (res.data) {
          this.dataMain.data = res.data.list;
          this.dataMain.total = res.data.total;
          this.dataMain.loading = false;
        }
      });
    },
    queryDeptList() {
      getSuperiorDept().then(res => {
        if (res.data) {
          this.formData.deptList = res.data;
        }
      });
    },
    getFinancialWorkloadtTypeDict() {
      getFinancialWorkloadtTypeDict().then(res => {
        if (res.data) {
          this.formData.WorkloadtTypeDict = res.data;
        }
      });
    },
    queryRegionList() {
      getRegionSelect().then(res => {
        if (res.data) {
          this.formData.regionList = res.data;
        }
      });
    },
    synchronization() {
      this.modalData.synchronizationLoading = true;
      syncFinancialWorkloadtType().then(res => {
        if (res.code === 0) {
          this.$Message.success('同步成功！');
        } else {
          this.$Message.error('同步失败，请重试！');
        }
        this.modalData.synchronizationLoading = false;
      });
    },
    showMod(val) {
      this.$refs['currentRow'].resetFields();
      this.queryDeptList();
      this.queryRegionList();
      if (val === 'create') {
        this.modalData.detailText = '新增工作量数据';
        this.modalData.modaldetail = true;
        this.formData.currentRow.fbz = '';
        this.isShow = false;
      } else if (val === 'update') {
        if (this.dataMain.handleRow.length === 0) {
          return this.$Message.error('请选择要修改的数据');
        }

        this.modalData.detailText = '新增工作量数据';
        this.formData.currentRow = {
          id: this.dataMain.handleRow.bzid,
          code: this.dataMain.handleRow.deptCode,
          pcode: this.dataMain.handleRow.deptpcode,
          name: this.dataMain.handleRow.deptName,
          sbm: this.dataMain.handleRow.deptSbm,
          // regionId: this.dataMain.handleRow.regionId,
        };
        if (this.formData.currentRow.pcode === '0') {
          this.isShow = true;
        }
        this.modalData.modaldetail = true;
      }
    },
    //删除方法
    delData() {
      if (this.dataMain.handleRow.length === 0) {
        return this.$Message.error('请选择要删除的数据');
      }
      this.modalData.deleteModal = true;
    },
    delOk() {
      if (this.dataMain.handleRow.length === 0) {
        return this.$Message.error('请选择要删除的数据');
      }
      deleteDeptBz(this.dataMain.handleRow.fid).then(res => {
        // setTimeout(msg, 0);
        if (res.code === 0) {
          this.$Message.success('删除成功');
          this.dataMain.handleRow = [];
          this.queryList();
        } else {
          this.$Message.error('删除失败');
        }
      });
    },
    delCancel() {
      this.modalData.deleteModal = false;
    },
    save() {
      this.$refs['currentRow'].validate(valid => {
        if (valid) {
          let obj = {
            fworkloadid: this.formData.currentRow.fworkloadid,
            fworkloadname: this.formData.currentRow.fworkloadname.split(',')[0],
            fdepartmentid: this.formData.currentRow.fdepartmentid,
            fdepartmentname: this.formData.currentRow.fdepartmentname.split(',')[0],
            fnumber: parseFloat(this.formData.currentRow.fnumber),
            fdate: dayjs(this.formData.currentRow.fdate).format('YYYY-MM-DD'),
            faddtime: '',
            finName: this.formData.currentRow.finName,
            fintype: this.formData.currentRow.fintype,
            fhid: this.s_orgID,
            ffb: this.formData.currentRow.ffb,
            fbz: this.formData.currentRow.fbz,
            ffflx: this.formData.currentRow.ffflx,
          };
          if (this.modalData.detailText === '新增工作量数据') {
            saveDeptBz(obj)
              .then(res => {
                if (res.code === 0) {
                  this.$Message.success('新增成功');
                  this.modalData.modaldetail = false;
                  this.queryList();
                } else {
                  this.$Message.error(res.msg);
                }
              })
              .catch(res => {
                console.log('error:' + res);
              });
          } else {
            updateDeptBz(this.formData.currentRow)
              .then(res => {
                if (res.code === 0) {
                  this.$Message.success('修改成功');
                  this.modalData.modaldetail = false;
                  this.dataMain.handleRow = [];
                  this.queryList();
                } else {
                  this.$Message.error(res.msg);
                }
              })
              .catch(res => {
                console.log('error:' + res);
              });
          }
        }
      });
    },
    handleRowChange(currentRow) {
      this.dataMain.handleRow = currentRow;
    },
    // 导出数据
    exportData() {
      downloadTemp('导出');
    },
    // 导出模板
    downloadTemplate() {
      downloadTemp('下载模板');
    },
    //导入前判断文件类型
    importBeforeUpload(file) {
      // 获取文件后缀名
      const fileExt = file.name.split('.').pop().toLocaleLowerCase();
      if (fileExt === 'xlsx' || fileExt === 'xls') {
        this.importData.formData = new FormData();
        this.importData.formData.append('file', file);
        this.importData.importName = this.importData.formData.get('file').name;
        importCheck(this.importData.formData).then(res => {
          if (res.data.length === 0) {
            this.importData.uploadModal = true;
          } else {
            this.importData.checkData = res.data;
            this.importData.errorModal = true;
          }
        });
      } else {
        this.$Notice.warning({
          title: '文件类型错误',
          desc: '文件：' + file.name + '不是EXCEL文件，请选择后缀为.xlsx或者.xls的EXCEL文件。',
        });
      }
      return false;
    },
    confirmUpload() {
      const msg = this.$Message.loading({
        content: '正在导入，请稍候...',
        duration: 0,
      });
      importDeptBz(this.importData.formData).then(res => {
        setTimeout(msg, 0);
        if (res.data.code === 0) {
          this.queryList();
          this.importData.uploadModal = false;
          this.$Message.success(res.data.msg);
        }
      });
    },
    cancelUpload() {
      //清空formdata
      this.importData.formData = null;
      this.importData.formData = new FormData();
      this.importData.uploadModal = false;
    },
    defaultDate(time) {
      // let myDate = new Date(y, m - 1, 0); //获取上个月最后一天
      let date = new Date();
      date.setDate(date.getDate() + time);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      let d = date.getDate();
      m = m < 10 ? '0' + m : m;
      d = d < 10 ? '0' + d : d;
      let time2 = y + '-' + m + '-' + d;
      return time2;
    },
    getDeptMsg(obj) {
      let list = obj.split(',');
      this.formData.currentRow.fdepartmentid = list[1];
    },
    getWorkload(obj) {
      let list = obj.split(',');
      this.formData.currentRow.fworkloadid = list[1];
    },
  },
  mounted() {
    this.searchData.mainData = [this.defaultDate(-30), this.defaultDate(-1)];
    this.queryData.beginDate = this.searchData.mainData[0];
    this.queryData.endDate = this.searchData.mainData[1];
    this.dataMain.tableHeight = window.innerHeight - this.$refs.table.$el.offsetTop - 95;
    window.onresize = () => {
      return (() => {
        this.dataMain.tableHeight = window.innerHeight - this.$refs.table.$el.offsetTop - 95;
      })();
    };
    this.queryList();
    this.queryDeptList();
    this.getFinancialWorkloadtTypeDict();
  },
};
</script>

<style scoped>
.mainDiv {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 10px;
}
</style>
